<template>
  <!-- align-start : alinea layout al centro-->
  <v-layout align-start overflow:auto>
    <v-flex overflow:auto>
      <!-- Card para dar espacio  -->
      <v-card class="mx-6" overflow:auto>
        <!-- ":search": metodo buscar / items: el array donde se encuentran los datos en console () 
      :headers="headers" Variables Cabeceras
      :items="empresas"  empresas es el array de informacin
      :search=  valor para filtrar
      sort-by  columna por la que se ordenaran
      class: clase
        -->
          <v-data-table
          :headers="headers"
          :items="listPrincipal"
          :search="search"
          :loading="loading"
          class="elevation-1"
          >
          <template v-slot:top>
            <v-toolbar flat color="white">
              <!-- Titulo principal del listado -->
              <v-toolbar-title class="blue--text" v-text="nameOpcion"></v-toolbar-title>
              <v-divider class="mx-4" inset vertical></v-divider>
              <v-spacer></v-spacer>
              <!-- Buscar / hide-details:ocultardetalles-->
              <v-text-field
                class="text-xs-center"
                v-model="search"
                append-icon="search"
                label="Bsqueda"
                single-line
                hide-details
              ></v-text-field>
              <v-spacer></v-spacer>
              <Buttons val_Param="Nuevo" @click.native="m_AbrirNuevo()" />
              <v-form ref="form" lazy-validation>
                <!-- Modal para Registrar / Actualizar -->
                <v-dialog v-model="dialog" max-width="700px">
                  <!-- Modal: Encabezado -->
                  <v-card>
                    <v-img class="white--text align-start" height="80px" v-bind:src="imageDialogo">
                      <v-card-title>
                        <span class="headline">{{ formTitle }}</span>
                      </v-card-title>
                    </v-img>
                    <!-- Modal: contenido -->
                    <v-card-text>
                      <v-container grid-list-md>
                        <v-row wrap>
                           <!-- Datos Principales -->
                          <v-col cols="4" sm="4" md="4" class="pa-0 ma-0">
                            <v-subheader>Datos Principales</v-subheader>
                          </v-col>
                          <v-col cols="8" sm="8" md="8" class="pa-0 ma-0">
                            <v-text-field class="body-2" :counter="200"  
                            :rules="[ v => !!v || 'Nombre es requerido',
                            v => (v && v.length <= 200) || 'Nombre debe ser menor a 200 caracteres']" 
                            v-model="noM_EMPRE" label="Nombre"></v-text-field>
                          </v-col>
                          <v-col cols="4" sm="4" md="4" class="pa-0 ma-0"></v-col>
                          <v-col cols="8" sm="8" md="8" class="pa-0 ma-0">
                            <v-text-field  class="body-2" :counter="30"  
                            :rules="[ v => !!v || 'Nombre Corto es requerido',
                            v => (v && v.length <= 30) || 'Nombre Corto debe ser menor a 30 caracteres']"  
                            hint="Por ejemplo, BBVA" v-model="noM_EMPRE_CORTA" label="Nombre Corto">
                            </v-text-field>
                          </v-col>
                          <v-col cols="4" sm="4" md="4" class="pa-0 ma-0"></v-col>
                          <v-col cols="5" sm="5" md="5" class="pt-0 mt-0 pb-0 pl-0">   
                            <v-text-field  class="body-2"  :counter="20"
                            :rules="[ v => !!v || 'Ruc es requerido',
                            v => (v && v.length <= 20) || 'Ruc debe ser menor a 20 caracteres']"
                             v-model="nuM_RUCSS" label="Ruc"></v-text-field>
                          </v-col>
                          <v-col cols="3" sm="3" md="3" class="pa-0 ma-0">
                            <v-checkbox  class="body-2" color="info" v-model="esT_CONTR" :label="`Contratista?`"></v-checkbox>
                          </v-col>
                          <v-col cols="4" sm="4" md="4" class="pa-0 ma-0"></v-col>
                          <v-col cols="8" sm="8" md="8" class="pa-0">
                            <v-file-input class="body-2"
                               ref="myUpload"  @change="onFileSelected"
                              :rules="[value => !!value || 'Logo es requerido',
                              value => !value || value.size < 5000000 || 'El tamao de la imagen debe ser menor a 5 MB!',]"
                              accept="image/png, image/jpeg"
                              :placeholder="holder"
                              label="Logo de tu empresa"
                              prepend-icon="mdi-camera"
                            >
                            </v-file-input>
                          </v-col>
                          <!-- Complementarios -->
                          <v-col cols="12" sm="12" md="12">
                            <v-divider></v-divider>
                          </v-col>
                          <v-col cols="4" sm="4" md="4" class="pa-0 ma-0">
                            <v-subheader>Complementarios</v-subheader>
                          </v-col>
                          <v-col cols="4" sm="4" md="4" class="pt-0 mt-0 pb-0 pl-0">
                            <!-- Combo -->
                            <v-select  class="body-2"
                              :rules="[ v => !!v || 'Actividad Econmica es requerida']"
                              v-model="idD_ACTIV_ECONOList"
                              :items="listActividades"
                              label="Seleccione Actividad Econmica"
                            ></v-select>
                          </v-col>
                          <v-col cols="4" sm="4" md="4" class="pa-0 ma-0">
                            <v-select  class="body-2"
                              :rules="[ v => !!v || 'Ubicacin Geografica es requerida']"
                              v-model="idD_UBICA_GEOGRList"
                              :items="listUbicacionGeografica"
                              label="Seleccione Ubicacin Geografica"
                            ></v-select>
                          </v-col>
                          <v-col cols="4" sm="4" md="4" class="pa-0 ma-0"></v-col>
                          <v-col cols="4" sm="4" md="4" class="pt-0 mt-0 pb-0 pl-0">
                            <v-text-field  class="body-2"
                             :rules="[ v => !!v || 'Telfono es requerido']"
                             v-model="nuM_TELEF" label="Telfono"></v-text-field>
                          </v-col>
                          <v-col cols="4" sm="4" md="4" class="pa-0 ma-0">
                            <v-text-field  class="body-2" v-model="deS_DIREC_EMAIL" label="Email"></v-text-field>
                          </v-col>
                          <v-col cols="4" sm="4" md="4" class="pa-0 ma-0"></v-col>
                          <v-col cols="4" sm="4" md="4" class="pt-0 mt-0 pb-0 pl-0">
                            <v-text-field  class="body-2" v-model="noM_DIREC" label="Direccin"></v-text-field>
                          </v-col>
                          <v-col cols="4" sm="4" md="4" class="pa-0 ma-0">
                            <v-text-field  class="body-2" v-model="noM_REPRE_LEGAL" label="Representante Legal"></v-text-field>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card-text>
                    <!-- Modal: Acciones
                    Botoneras para Cancelar y Guardar
                    -->
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <Buttons val_Param="Cancelar" @click.native="m_close" />
                      <Buttons val_Param="Guardar" @click.native="m_guardar" />
                    </v-card-actions>
                  </v-card>
                </v-dialog>
                <!-- Fin de dialogo para nuevo o actualizar registro -->
              </v-form>
              <!-- Inicio de Dialogo para Activar o Desactivar -->
              <v-dialog v-model="adModal" max-width="400">
                <v-card>
                  <v-card-title  class="headline" v-if="adAccion==1">Activar Item?</v-card-title>
                  <v-card-title  class="headline" v-if="adAccion==2">Desactivar Item?</v-card-title>
                  <v-card-text>
                    Ests a punto de
                    <span v-if="adAccion==1">Activar</span>
                    <span v-if="adAccion==2">Desactivar</span>
                    el tem {{adNombre}}
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <Buttons
                      val_Param="Cancelar"
                      @click.native="m_activarDesactivarCerrar()"
                    ></Buttons>
                    <Buttons
                      v-if="adAccion == 1"
                      val_Param="Activar"
                      @click.native="m_activarDesactivar()"
                    ></Buttons>
                    <Buttons
                      v-if="adAccion == 2"
                      val_Param="Desactivar"
                      @click.native="m_activarDesactivar()"
                    ></Buttons>
                  </v-card-actions>
                </v-card>
              </v-dialog>
              <!-- Fin de dialogo para activar o desactivar registro -->
            </v-toolbar>
          </template>
          <!-- Opciones manuales para las columnas -->
          <!-- Columna de Imagen -->
          <template v-slot:item.noM_RUTAS_LOGOS_View="{ item }">
            <img v-bind:src="item.noM_RUTAS_LOGOS_View" width="100" aspect-ratio="1.7" />
          </template>
          <!-- Columna de Estado de Registro esT_REGIS -->
          <template v-slot:item.esT_REGIS="{ item }">
            <!-- Paso el parametro del registro -->
            <Icons
              v-bind:val_Param="item.esT_REGIS"
              small
              @click.native="m_activarDesactivarMostrar(item)"/>
          </template>
          <!-- Columna de Opciones -->
          <template v-slot:item.opciones="{ item }">
            <v-icon small class="mr-2" @click="editItem(item)">edit</v-icon>
          </template>
          <!-- Si no existe informacin mostrara un boton de Reset -->
          <template v-slot:no-data>
            <Buttons val_Param="Refrescar"  @click.native="m_ListarPrincipal()"></Buttons>
          </template>
        </v-data-table>
      </v-card>
      <!-- -->
    </v-flex>
  </v-layout>
</template>


<script>
import axios from "axios";
import Buttons from "@/components/Buttons/Buttons.vue";
import Icons from "@/components/Buttons/Icons.vue";

export default {
  name: "Empresas",
  components: {
    Buttons,
    Icons,
  },
  data: () => {
    return {
      /** Variables de conexion */
      imageDialogo: "",
      cod_USUAR_Conexion: "",
      idd_EMPRE_Conexion: 0,
      iddCuenta_Conexion: "",
      listPrincipal: [],

      /* Variables de la pantalla  NO TOCAR*/
      search: "",
      editedIndex: -1,
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: "",
      dialog: false,
      loading: true,
      holder: "Sube una imagen",

      /* Nombres de la opcin y el controller BackEnd */
      nameOpcion: "Empresa", //Nombre que se muestra en la pantalla
      nameAxios: "Empresas", //Nombre que se utiliza para buscar el backend

      /** Columnas de la tabla */
      headers: [
        {
          text: "Condicin ",
          value: "esT_REGIS",
          sortable: false,
          align: "center"
        },
        { text: "Tipo", value: "esT_CONTR_VALOR", sortable: false },
        { text: "Imagen", value: "noM_RUTAS_LOGOS_View", sortable: true },
        { text: "Empresa", value: "noM_EMPRE", sortable: true },
        { text: "Ruc", value: "nuM_RUCSS", sortable: true },
        { text: "Telfono", value: "nuM_TELEF", sortable: true },
        { text: "Opciones", value: "opciones", sortable: false, align: "center" }
      ],

      /*******Variables para la entidad a tratar******/
      /*Combos*/
      idD_ACTIV_ECONOList: "",
      idD_UBICA_GEOGRList: "",
      listActividades: [],
      listUbicacionGeografica: [],

      /*Tabla*/
      idD_EMPRE: "",
      coD_EMPRE: "",
      coD_EMPRE: "",
      noM_EMPRE: "",
      noM_EMPRE_CORTA: "",
      nuM_RUCSS: "",
      idD_ACTIV_ECONO: "",
      idD_UBICA_GEOGR: "",
      nuM_TELEF: "",
      deS_DIREC_EMAIL: "",
      noM_DIREC: "",
      noM_REPRE_LEGAL: "",
      noM_RUTAS_LOGOS: "",
      esT_REGIS: "",
      esT_CONTR: false,
      idD_CUENT: "",
    };
  },

  computed: {
    /****************************************/
    /* Obtiene el nombre para nuevo o actualizar */
    /****************************************/
    formTitle() {
      return this.editedIndex === -1
        ? "Nueva " + this.nameOpcion
        : "Actualizar " + this.nameOpcion;
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    /****************************************/
    /* Operaciones que se cargan al iniciar la ventana */
    /** Cargamos variables de conexin */
    /****************************************/
    this.idd_EMPRE_Conexion = parseInt(localStorage.getItem("codEmpresa"));
    this.cod_USUAR_Conexion = localStorage.getItem("codUsuario");
    this.iddCuenta_Conexion = localStorage.getItem("iddCuenta");
    this.imageDialogo = this.$imageDialog;

    this.m_listarActividades();
    this.m_listarUbicacionGeografica();
    this.m_ListarPrincipal();
  },

  methods: {

     onFileSelected(file) {
      let reader = new FileReader();
      reader.onload = (e) => {
        this.noM_RUTAS_LOGOS = e.target.result;
      };
      reader.readAsDataURL(file);
    },

    /****************************************/
    /* Metodo para listar actividades economicas */
    /****************************************/

    m_listarActividades() {
      let me = this;
      var arrayListado = [];
      var optionAxios = {
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + this.$store.state.token
        }
      };
      axios
        .get("api/ActivEconomicas/ListarCombo", optionAxios)
        .then(function(response) {
          arrayListado = response.data;
          arrayListado.map(function(x) {
            me.listActividades.push({
              text: x.deS_ACTIV_ECONO,
              value: x.idD_ACTIV_ECONO
            });
          });
          //me.articulos = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    /****************************************/
    /* Metodo para listar Ubicaciones Geograficas */
    /****************************************/
    m_listarUbicacionGeografica() {
      let me = this;
      var arrayListado = [];
      var optionAxios = {
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + this.$store.state.token
        }
      };
      axios
        .get("api/UbicacionesGeograficas/ListarCombo", optionAxios)
        .then(function(response) {
          arrayListado = response.data;
          arrayListado.map(function(x) {
            me.listUbicacionGeografica.push({
              text: x.deS_UBICA_GEOGR,
              value: x.idD_UBICA_GEOGR
            });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    /****************************************/
    /* Metodo para listar Empresas       */
    /****************************************/
    m_ListarPrincipal() {
      let me = this;
      var optionAxios = {
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + this.$store.state.token
        }
      };
      axios
        .get("api/"+ this.nameAxios +"/Listar", optionAxios)
        .then(function(response) {
          me.listPrincipal = response.data;
        })
        .catch(function(error) {
          console.log(error);
        })
        .finally(() => (this.loading = false));
    },

    /****************************************/
    /* Metodo que carga las variables cuando selcciono editar */
    /****************************************/
    editItem(item) {
      this.idD_EMPRE = item.idD_EMPRE;
      this.coD_EMPRE = item.coD_EMPRE;
      this.noM_EMPRE = item.noM_EMPRE;
      this.noM_EMPRE_CORTA = item.noM_EMPRE_CORTA;
      this.nuM_RUCSS = item.nuM_RUCSS;
      this.idD_ACTIV_ECONOList = item.idD_ACTIV_ECONO;
      this.idD_UBICA_GEOGRList = item.idD_UBICA_GEOGR;
      this.nuM_TELEF = item.nuM_TELEF;
      this.deS_DIREC_EMAIL = item.deS_DIREC_EMAIL;
      this.noM_DIREC = item.noM_DIREC;
      this.noM_REPRE_LEGAL = item.noM_REPRE_LEGAL;
      this.noM_RUTAS_LOGOS = item.noM_RUTAS_LOGOS;
      this.coD_USUAR_CREAC = item.coD_USUAR_CREAC;
      this.coD_USUAR_MODIF = item.coD_USUAR_MODIF;
      this.esT_CONTR = item.esT_CONTR;
      this.editedIndex = 1;
      this.dialog = true;
      this.holder="Vuelva a cargar una imagen para actualizar";
    },

    /****************************************/
    /* Metodo para cerrar dialogo          */
    /****************************************/
    close() {
      this.dialog = false;
      this.limpiar();
    },

    /****************************************/
    /* Metodo para limpiar campos          */
    /****************************************/
    limpiar() {
      this.id = "";
      this.noM_EMPRE = "";
      this.noM_EMPRE_CORTA = "";
      this.noM_RUTAS_LOGOS = "";
      this.holder="Sube una imagen";
      this.editedIndex = -1;
    },

    /****************************************/
    /* Metodo para guardar/actualizar       */
    /****************************************/
    m_guardar() {
      //si this.validar es igual a 1 significa que tiene un error y va retornar al modal
      if (this.m_Validar()) {
        return;
      }
      var optionAxios = {
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + this.$store.state.token
        }
      };
      if (this.editedIndex > -1) {
        let me = this;
        let model = {
          idD_EMPRE: me.idD_EMPRE,
          coD_EMPRE: me.coD_EMPRE,
          noM_EMPRE: me.noM_EMPRE,
          noM_EMPRE_CORTA: me.noM_EMPRE_CORTA,
          nuM_RUCSS: me.nuM_RUCSS,
          idD_ACTIV_ECONO: me.idD_ACTIV_ECONOList,
          idD_UBICA_GEOGR: me.idD_UBICA_GEOGRList,
          nuM_TELEF: me.nuM_TELEF,
          deS_DIREC_EMAIL: me.deS_DIREC_EMAIL,
          noM_DIREC: me.noM_DIREC,
          noM_REPRE_LEGAL: me.noM_REPRE_LEGAL,
          noM_RUTAS_LOGOS: this.noM_RUTAS_LOGOS,
          coD_USUAR_CREAC: me.cod_USUAR_Conexion,
          coD_USUAR_MODIF: me.cod_USUAR_Conexion,
          esT_CONTR: me.esT_CONTR
        };
        axios
          .put("api/" + this.nameAxios + "/Actualizar", model, optionAxios)
          .then(function(response) {
            me.m_close();
            me.m_limpiar();
            me.m_ListarPrincipal();
          })
          .catch(function(error) {
            console.log(error);
            console.log(error.request);
            console.log(error.request.response);
          });
      } else {
        //Cdigo para guardar
        debugger
        var me = this;
        let model = {
          coD_EMPRE: me.coD_EMPRE,
          noM_EMPRE: me.noM_EMPRE,
          noM_EMPRE_CORTA: me.noM_EMPRE_CORTA,
          nuM_RUCSS: me.nuM_RUCSS,
          idD_ACTIV_ECONO: me.idD_ACTIV_ECONOList,
          idD_UBICA_GEOGR: me.idD_UBICA_GEOGRList,
          nuM_TELEF: me.nuM_TELEF,
          deS_DIREC_EMAIL: me.deS_DIREC_EMAIL,
          noM_DIREC: me.noM_DIREC,
          noM_REPRE_LEGAL: me.noM_REPRE_LEGAL,
          noM_RUTAS_LOGOS: this.noM_RUTAS_LOGOS,
          coD_USUAR_CREAC: me.cod_USUAR_Conexion,
          coD_USUAR_MODIF: me.cod_USUAR_Conexion,
          esT_CONTR: me.esT_CONTR,
          idD_CUENT: me.iddCuenta_Conexion
        };
        debugger
        axios
          .post("api/" + this.nameAxios + "/Guardar", model, optionAxios)
          .then(function(response) {
            me.m_close();
            me.m_limpiar();
            me.m_ListarPrincipal();
          })
          .catch(function(error) {
            console.log(error);
            console.log(error.request.response);
          });
      }
    },

    /****************************************/
    /* Metodo para validar antes de guardar */
    /****************************************/

    m_Validar() {
      var validateForm = this.$refs.form.validate();
      return validateForm == true ? 0 : 1;
    },

    /****************************************/
    /* Metodo para abrir modal para activar o desactivar */
    /****************************************/
    m_activarDesactivarMostrar(item) {
      var accion = item.esT_REGIS == true ? 2 : 1;

      this.adModal = 1;
      this.adNombre = item.noM_EMPRE;
      this.adId = item.idD_EMPRE;

      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },

    /************************************************/
    /* Metodo para cerrar Modal de Activar/Desactivar */
    /************************************************/

    m_activarDesactivarCerrar() {
      this.adModal = 0;
    },
        
    /****************************************/
    /* Metodo para activar o desactivar     */
    /****************************************/
    m_activarDesactivar() {
      let me = this;
      axios
        .put("api/" + this.nameAxios + "/Estado/" + this.adId, {})
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.m_ListarPrincipal();
        })
        .catch(function(error) {
          console.log(error.request.response);
        });
    },

    /************************************************/
    /* Metodo para abrir pantalla de nuevo registro */
    /************************************************/

    m_AbrirNuevo() {
      this.$refs.form.reset();
      this.dialog = true;
    },
    /****************************************/
    /* Metodo para cerrar dialogo de Nuevo y Editar */
    /****************************************/
    m_close() {
      this.dialog = false;
      this.m_limpiar();
    },
  }
};
</script>