

<template>
  <v-container fill-height>
    <v-layout align-center justify-center>
      <v-flex xs12 sm5 md5>
        <v-form>
          <v-card class="elevation-12">
            <v-toolbar dark color="blue">
              <v-toolbar-title>Bienvenido a MyperSSoma</v-toolbar-title>
            </v-toolbar>
            <v-alert color="error" :value="error" icon="close">{{error}}</v-alert>
            <v-card-text>
              <v-text-field
                v-model="usuario"
                prepend-icon="person"
                name="login"
                label="Usuarios"
                type="text"
              ></v-text-field>

              <v-text-field
                v-model="password"
                prepend-icon="lock"
                name="password"
                label="Contrasea"
                type="password"
              ></v-text-field>
            </v-card-text>
            <v-divider light></v-divider>
            <v-card-actions>
              <!--               <v-btn to="/signup" round color="indigo" dark>Sign up</v-btn>
              -->
              <v-spacer></v-spacer>
              <!--          <v-btn round color="primary" dark @click.prevent="login()">
                Login
                
              </v-btn>-->
              <v-btn @click="ingresar" color="primary">
                Ingresar
                <v-icon>keyboard_arrow_right</v-icon>
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-form>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      usuario: "",
      password: "",
      error: null
    };
  },
  methods: {
    ingresar() {
      this.error = null;
      axios
        .post("api/Usuarios/Login", {
          usuario: this.usuario,
          password: this.password
        })
        .then(respuesta => {
          return respuesta.data;
        })
        .then(data => {
          this.$store.dispatch("guardarToken", data.token);
          this.$router.push({ name: "Home" });
          location.reload();
        })
        .catch(err => {
          if (err.response.status == 400) {
            this.error = "No es un email vlido";
          } else if (err.response.status == 404) {
            this.error = "Usuario o password incorrectos";
          } else {
            console.log(err);
            this.error = "Ocurrio un error";
          }
        });
    }
  }
};
</script>

<style>
.v-content {
  /*background-image: url("../assets/Login4.png");*/
  -webkit-font-smoothing: auto;
  -moz-osx-font-smoothing: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Roboto, sans-serif;
  position: unset;
  z-index: 1;
  background-size: 1840px;
  background-position: 10% 10%;
}


</style>
