<template>
  <v-data-table
    :headers="headers"
    :items="listSistemas"
    :search="search"
    sort-by="calories"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar flat color="white">
        <v-toolbar-title class="blue--text">Usuario </v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <template>
          <v-btn small color="#0097A7" dark class="mb-2" @click="m_Regresar()">Regresar</v-btn>
        </template>
        <!-- v-spacer:spacio-->
        <v-spacer></v-spacer>

        <!-- Buscar / hide-details:ocultardetalles-->
        <v-text-field
          class="text-xs-center"
          v-model="search"
          append-icon="search"
          label="Bsqueda"
          single-line
          hide-details
        ></v-text-field>
        <v-spacer></v-spacer>

        <!-- Modal para Activar / Desactivar -->
        <v-dialog v-model="adModal" max-width="290">
          <v-card>
            <v-card-title class="headline" v-if="adAccion==1">Activar Item?</v-card-title>
            <v-card-title class="headline" v-if="adAccion==2">Desactivar Item?</v-card-title>
            <v-card-text>
              Ests a punto de
              <span v-if="adAccion==1">Activar</span>
              <span v-if="adAccion==2">Desactivar</span>
              el tem {{adNombre}}
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                style="color:white"
                color="green darken-1"
                @click="activarDesactivarCerrar"
              >Cancelar</v-btn>
              <v-btn
                v-if="adAccion==1"
                style="color:white"
                color="orange darken-4"
                @click="m_activarDesactivar"
              >Activar</v-btn>
              <v-btn
                v-if="adAccion==2"
                style="color:white"
                color="orange darken-4"
                @click="m_activarDesactivar"
              >Desactivar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>

    <template v-slot:item.noM_RUTAS_LOGOS_View="{ item }">
      <img v-bind:src="item.noM_RUTAS_LOGOS_View" width="100" aspect-ratio="1.7" />
    </template>

    <template v-slot:item.esT_REGIS="{ item }">
      <template v-if="item.esT_REGIS">
        <v-btn text class="gray" @click="m_ActivarDesactivar(item)">
          <v-icon smal color="green">check_box</v-icon>
        </v-btn>
      </template>
      <template v-else>
        <v-btn text class="gray" @click="m_ActivarDesactivar(item)">
          <v-icon smal color="green">check_box_outline_blank</v-icon>
        </v-btn>
      </template>
    </template>

    <template v-slot:item.detalle="{ item }">
      <template>
        <v-btn small dark color="#7E57C2" @click="mostrarNuevo(item.idD_SISTE)">Divisiones</v-btn>
      </template>
    </template>
    <template v-slot:no-data>
      <v-btn color="primary" @click="listar">Reset</v-btn>
    </template>
  </v-data-table>
</template>


<script>
import axios from "axios";

export default {
  props: {
    // Simple type validation
    userName_Param: String
  },
  //name: "Actividad Econmica",
  data: () => {
    return {
      /** Variables de conexion */
      cod_USUAR_Conexion: "",
      idd_EMPRE_Conexion: 0,
      /** Variables del listado general */
      nameOpcion: "Sistema",
      nameOpcionDetalle: "Opcin",
      selectedFile: null,
      image: "",
      listSistemas: [],
      listOpciones: [],
      listOpcionesPadres: [],
      idD_OPCIOList: 0,
      drawer: null,
      headers: [
        { text: "Empresa", value: "noM_EMPRE", sortable: true },
        {
          text: "Condicin ",
          value: "esT_REGIS",
          sortable: false,
          align: "center"
        },
        { text: "", value: "detalle", sortable: false }
      ],
      /* Variables de la lista */
      userName: "",
      idD_EMPRE: 0,

      dialog: false,
      vContainerSistema: 0,
      search: "",
      editedIndex: -1,
      valida: 0,
      validaMensaje: [],
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: ""
    };
  },

  computed: {
    /****************************************/
    /* Metodo que funcionan dentro de la ventana */
    /****************************************/
    formTitle() {
      return this.editedIndex === -1
        ? "Nueva " + this.nameOpcion
        : "Actualizar " + this.nameOpcion;
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    /****************************************/
    /* Operaciones que se cargan al iniciar la ventana */
    /****************************************/
    this.idd_EMPRE_Conexion = parseInt(localStorage.getItem("codEmpresa"));
    this.cod_USUAR_Conexion = localStorage.getItem("codUsuario");
    this.listar();
  },

  methods: {
    m_ActivarDesactivar(item) {
      var prueba = item;
      let me = this;
      let model = {
        idD_EMPRE: item.idD_EMPRE,
        esT_REGIS: item.esT_REGIS,
        idD_EMPRE_USUAR: item.idD_EMPRE_USUAR,
        userName: this.userName_Param,
        coD_USUAR_CREAC: me.cod_USUAR_Conexion,
        coD_USUAR_MODIF: me.cod_USUAR_Conexion
      };
  var optionAxios = {
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + this.$store.state.token
        }
      };
      axios
        .post("api/Empresas/ActualizarAccesos", model, optionAxios)
        .then(function(response) {
          me.listar();
          me.m_limpiar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    mostrarNuevo(id) {
      this.idD_SISTE_OPCIO = id;
      this.listarOpcionesPorSistema(id);
      this.listarOpcionesPadresPorSistema(id);
      this.vContainerSistema = 1;
    },

    m_Regresar() {
      this.$emit("CerrarEmpresas", "prueba");
      this.vContainerSistema = 0;
    },

    /****************************************/
    /* Metodo para listar Empresas       */
    /****************************************/
    listar() {
      let me = this;
      var optionAxios = {
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + this.$store.state.token
        }
      };
      axios
        .get(
          "api/Empresas/ListarPorAccesosUsuario/" + this.userName_Param,
          optionAxios
        )
        .then(function(response) {
          me.listSistemas = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    /****************************************/
    /* Metodo para cerrar dialogo          */
    /****************************************/
    close() {
      this.dialog = false;
      this.limpiar();
    },

    /****************************************/
    /* Metodo para limpiar campos          */
    /****************************************/
    limpiar() {
      this.id = "";
      this.validaMensaje = [];
      this.editedIndex = -1;
    },

    /****************************************/
    /* Metodo para abrir modal para activar o desactivar */
    /****************************************/

    activarDesactivarSisMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.noM_SISTE;
      this.adId = item.idD_SISTE;

      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },

    /****************************************/
    /* Metodo para abrir modal para activar o desactivar */
    /****************************************/

    activarDesactivarMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.noM_OPCIO;
      this.adId = item.idD_OPCIO;

      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },

    /****************************************/
    /* Metodo para cerrar modal si no se desea activar/desactivar */
    /****************************************/
    activarDesactivarCerrar() {
      this.adModal = 0;
    }
  }
};
</script>