<template>
  <!-- align-start : alinea layout al centro-->
  <v-layout align-start>
    <v-flex>
      <!-- Codigo de vuetify-->
      <v-card shaped class="mx-6">
        <!-- ":search": metodo buscar / items: el array donde se encuentran los datos en console () -->
        <v-data-table
          :headers="headers"
          :items="peligros"
          :search="search"
          sort-by="deS_TIPOS_PELIG"
          class="elevation-1"
        >
          <template v-slot:top>
            <v-toolbar flat color="white">
              <v-toolbar-title class="blue--text" v-text="nameOpcion"></v-toolbar-title>
              <v-divider class="mx-4" inset vertical></v-divider>
              <!-- v-spacer:spacio-->
              <v-spacer></v-spacer>
              <!-- Buscar / hide-details:ocultardetalles-->
              <v-text-field
                class="text-xs-center"
                v-model="search"
                append-icon="search"
                label="Bsqueda"
                single-line
                hide-details
              ></v-text-field>
              <v-spacer></v-spacer>

              <!-- Dialogo para Registrar / Actualizar -->
              <v-dialog v-model="dialog" max-width="500px">
                <template v-slot:activator="{ on }">
                  <v-btn color="success" dark class="mb-2" v-on="on">Nuevo Registro</v-btn>
                </template>
                <!-- Modal: Encabezado -->
                <v-card>
                  <v-img class="white--text align-start" height="100px" v-bind:src="imageDialogo">
                    <v-card-title>
                      <span class="headline">{{ formTitle }}</span>
                    </v-card-title>
                  </v-img>
                  <!-- Modal: contenido -->
                  <v-card-text>
                    <v-container grid-list-md>
                      <v-row wrap>
                        <v-col cols="12" sm="12" md="12">
                          <v-text-field v-model="deS_TIPOS_PELIG" label="Nombre"></v-text-field>
                        </v-col>

                        <!-- Modal que muestra los mensajes de error -->
                        <v-col cols="12" sm="12" md="12" v-show="valida">
                          <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>
                  <!-- Modal: Acciones -->
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                    <v-btn color="blue darken-1" text @click="guardar">Guardar</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>

              <!-- Dialogo para Activar o Desactivar el registro -->
              <v-dialog v-model="adModal" max-width="400">
                <v-card>
                  <v-card-title class="headline" v-if="adAccion==1">Activar Item?</v-card-title>
                  <v-card-title class="headline" v-if="adAccion==2">Desactivar Item?</v-card-title>
                  <v-card-text>
                    Ests a punto de
                    <span v-if="adAccion==1">Activar</span>
                    <span v-if="adAccion==2">Desactivar</span>
                    el tem {{adNombre}}
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                      style="color:white"
                      color="green darken-1"
                      @click="activarDesactivarCerrar"
                    >Cancelar</v-btn>
                    <v-btn
                      v-if="adAccion==1"
                      style="color:white"
                      color="orange darken-4"
                      @click="m_activarDesactivar"
                    >Activar</v-btn>
                    <v-btn
                      v-if="adAccion==2"
                      style="color:white"
                      color="orange darken-4"
                      @click="m_activarDesactivar"
                    >Desactivar</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>

          <template v-slot:item.esT_REGIS="{ item }">
            <v-simple-checkbox v-model="item.esT_REGIS" disabled></v-simple-checkbox>
          </template>

          <template v-slot:item.opciones="{ item }">
            <v-icon small class="mr-2" @click="editItem(item)">edit</v-icon>


            <template v-if="item.esT_REGIS">
              <v-icon small @click="activarDesactivarMostrar(2,item)">block</v-icon>
            </template>
            <template v-else>
              <v-icon small @click="activarDesactivarMostrar(1,item)">check</v-icon>
            </template>
          </template>
          <template v-slot:no-data>
            <v-btn color="primary" @click="listar">Reset</v-btn>
          </template>
        </v-data-table>
      </v-card>
      <!-- -->
    </v-flex>
  </v-layout>
</template>


<script>
import axios from "axios";

export default {
  name: "Peligros",
  data: () => {
    return {
      /** Variables de conexion */
      imageDialogo: "",
      cod_USUAR_Conexion: "",
      idd_EMPRE_Conexion: 0,
      nameOpcion: "Peligro",
      peligros: [],
      drawer: null,
      dialog: false,
      headers: [
        { text: "Opciones", value: "opciones", sortable: false },
        { text: "Descripcin", value: "deS_TIPOS_PELIG", sortable: true },
        {
          text: "Condicin ",
          value: "esT_REGIS",
          sortable: false,
          align: "center"
        }
      ],
      /*Variables Combos*/
      idD_EMPREList: "",
      listEmpresas: [],

      /*Variables */
      idD_PELIG: "",
      coD_TIPOS_PELIG: "",
      deS_TIPOS_PELIG: "",
      coD_USUAR_CREAC: "",
      feC_USUAR_CREAC: "",
      coD_USUAR_MODIF: "",
      feC_USUAR_MODIF: "",
      esT_REGIS: "",

      search: "",
      editedIndex: -1,
      id: "",
      nombre: "",
      descripcion: "",
      valida: 0,
      validaMensaje: [],
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: ""
    };
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? "Nueva " + this.nameOpcion
        : "Actualizar " + this.nameOpcion;
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    /** Cargamos variables de conexin */
    this.idd_EMPRE_Conexion = parseInt(localStorage.getItem("codEmpresa"));
    this.cod_USUAR_Conexion = localStorage.getItem("codUsuario");
    this.listar();
    this.imageDialogo = this.$imageDialog;
  },

  methods: {
    //Metodo Listar
    listar() {
      let me = this;
      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      let header = { Authorization: "Bearer" + this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get(
          "api/Peligros/ListarPorEmpresa/" + this.idd_EMPRE_Conexion,
          optionAxios
        )
        .then(function(response) {
          me.peligros = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    /****************************************/
    /* Metodo que carga las variables cuando selcciono editar */
    /****************************************/
    editItem(item) {
      this.idD_PELIG = item.idD_PELIG;
      this.coD_TIPOS_PELIG = item.coD_TIPOS_PELIG;
      this.deS_TIPOS_PELIG = item.deS_TIPOS_PELIG;
      this.coD_USUAR_CREAC = item.coD_USUAR_CREAC;
      this.coD_USUAR_MODIF = item.coD_USUAR_MODIF;
      this.editedIndex = 1;
      this.dialog = true;
    },

    /****************************************/
    /* Metodo para cerrar dialogo de Nuevo y Editar */
    /****************************************/
    close() {
      this.dialog = false;
      this.limpiar();
    },

    /****************************************/
    /* Metodo para limpiar campos */
    /****************************************/
    limpiar() {
      this.id = "";
      this.deS_DIVIS = "";
      this.validaMensaje = [];
      this.editedIndex = -1;
    },
    /****************************************/
    /* Metodo para guardar y actualizar     */
    /****************************************/
    guardar() {
      /** Si Validar devuevle 1 no realizara ninguna accion */
      if (this.validar()) {
        return;
      }
      if (this.editedIndex > -1) {
        //Cdigo para editar
        let me = this;
        let model = {
            idD_PELIG: me.idD_PELIG,
            idD_EMPRE: me.idd_EMPRE_Conexion,
            coD_TIPOS_PELIG: me.coD_TIPOS_PELIG,
            deS_TIPOS_PELIG: me.deS_TIPOS_PELIG,
            coD_USUAR_MODIF: me.cod_USUAR_Conexion
        };        
        axios
          .put("api/Peligros/Actualizar", model)
          .then(function(response) {
            me.close();
            me.listar();
            me.limpiar();
          })
          .catch(function(error) {
            console.log(error);
          });
      } else {
        //Cdigo para guardar
        let me = this;
        let model = {
            idD_EMPRE: me.idd_EMPRE_Conexion,
            coD_TIPOS_PELIG: me.coD_TIPOS_PELIG,
            deS_TIPOS_PELIG: me.deS_TIPOS_PELIG,            
            coD_USUAR_CREAC: me.cod_USUAR_Conexion,
            coD_USUAR_MODIF: me.cod_USUAR_Conexion
        };
        axios
          .post("api/Peligros/Guardar", model)
          .then(function(response) {
            me.close();
            me.listar();
            me.limpiar();
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },

    validar() {
      this.valida = 0;
      this.validaMensaje = [];

      if (!this.deS_TIPOS_PELIG) {
        this.validaMensaje.push("Ingrese el nombre del peligro");
      }

      //si es mayor a 0
      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },

    /****************************************/
    /* Metodo para abrir modal para activar o desactivar */
    /****************************************/

    activarDesactivarMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.deS_TIPOS_PELIG;
      this.adId = item.idD_PELIG;

      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },

    /****************************************/
    /* Metodo para activar o desactivar     */
    /****************************************/
    m_activarDesactivar() {
      let me = this;
      axios
        .put("api/Peligros/Estado/" + this.adId, {})
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
        })
        .catch(function(error) {
          console.log(error.request.response);
        });
    },

    activarDesactivarCerrar() {
      this.adModal = 0;
    }
  }
};
</script>