<script>
import axios from "axios";
/* import jsPDF from "jspdf";
import html2canvas from "html2canvas"; */
import fichaTrabajadorForm from "@/components/DatosPersonales/fichaTrabajadorForm.vue";
//import MenuBar from '@/components/Home/MenuBar.vue'

export default {
  name: "fichaTrabajador",
  components: {
    fichaTrabajadorForm
  },
  data: () => {
    return {
      comprobanteModal: 0,
      idD_TRABA_Param: 0,
      /** Tabs */
      loading: true,
      currentItem: "tab-Web",
      items: ["Datos Personales", "Datos Empresa"],
      more: ["News", "Maps", "Books", "Flights", "Apps"],
      text:
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.",

      /** Variables de conexion */
      cod_USUAR_Conexion: "",
      idd_EMPRE_Conexion: 0,
      /** Variables del listado general */
      nameOpcion: "Ficha Trabajador",
      fichaTrabajador: [],
      /* Cabecera de la tabla principal */
      headers: [
        { text: "Opciones", value: "opciones", sortable: false },
        {
          text: "Tipo Empresa",
          value: "esT_CONTR",
          sortable: false,
          align: "center"
        },
        { text: "Empresa", value: "noM_EMPRE", sortable: true },
        { text: "Imagen", value: "urL_RUTAS_FOTOS", sortable: true },

        { text: "Cdigo Trabajador", value: "coD_TRABA", sortable: true },
        { text: "Nombre Completo", value: "noM_TRABA", sortable: true },
        {
          text: "Situacin Trabajador ",
          value: "esT_ACTIV",
          sortable: false,
          align: "center"
        }
      ],
      /*Variables Combos*/
      idD_EMPREList: "",
      listEmpresas: [],

      /*Variables Tabla*/
      idD_TRABA: "",
      idD_EMPRE: "",
      coD_EMPRE: "",
      noM_EMPRE: "",
      coD_TRABA: "",
      noM_TRABA: "",
      esT_ACTIV: "",
      esT_CONTR: "",

      /*Variables Basicas Formulario*/
      dialog: false,
      search: "",
      editedIndex: -1,
      valida: 0,
      validaMensaje: [],
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      verNuevo: 0,
      adId: ""
    };
  },

  computed: {
    /****************************************/
    /* Metodo que funcionan dentro de la ventana */
    /****************************************/
    formTitle() {
      return this.editedIndex === -1
        ? "Nueva " + this.nameOpcion
        : "Actualizar " + this.nameOpcion;
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    /****************************************/
    /* Operaciones que se cargan al iniciar la ventana */
    /****************************************/
    this.idd_EMPRE_Conexion = parseInt(localStorage.getItem("codEmpresa"));
    this.cod_USUAR_Conexion = localStorage.getItem("codUsuario");
    this.listar();
  },

  methods: {
    crearPDF() {
      var quotes = document.getElementById("factura");
      html2canvas(quotes).then(function(canvas) {
        var imgData = canvas.toDataURL("image/png");
        var imgWidth = 210;
        var pageHeight = 295;
        var imgHeight = (canvas.height * imgWidth) / canvas.width;
        var heightLeft = imgHeight;
        var doc = new jsPDF();
        /*         var doc = new jsPDF({
          orientation: "landscape",
          unit: "in",
          format: [4, 2]
        }); */
        var position = 0;

        /*        
        var elementHandler = {
          "#ignorePDF": function(element, renderer) {
            return true;
          }
        };
        doc.fromHTML(quotes, 15, 15, {
          width: 180,
          elementHandlers: elementHandler
        }); */
        doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
        doc.save("ComprobanteVenta.pdf");
      });
    },
    /****************************************/
    /*Tabs*/
    /****************************************/
    addItem(item) {
      const removed = this.items.splice(0, 1);
      this.items.push(...this.more.splice(this.more.indexOf(item), 1));
      this.more.push(...removed);
      this.$nextTick(() => {
        this.currentItem = "tab-" + item;
      });
    },
    /****************************************/
    /*Muestra y oculta vista de nuevo Registro*/
    /****************************************/
    mostrarNuevo() {
      this.idD_TRABA_Param = 0;
      this.verNuevo = 1;
    },

    ocultarNuevo() {
      this.verNuevo = 0;
      this.limpiar();
      this.listar();
    },

    /****************************************/
    /* Metodo que carga las variables cuando selcciono editar */
    /****************************************/
    editItem(item) {
      this.idD_TRABA_Param = item.idD_TRABA;

      this.verNuevo = 1;
    },
    /****************************************/
    /* Metodo para listar Empresas       */
    /****************************************/
    listar() {
      let me = this;
      //me.loading = true;
      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      let header = { Authorization: "Bearer" + this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("api/trabajadores/Listar", optionAxios)
        .then(function(response) {
          me.fichaTrabajador = response.data;
        })
        .catch(function(error) {
          console.log(error);
        })
        .finally(() => (this.loading = false));
    },

    activarDesactivarMostrar(accion, item) {
      this.comprobanteModal = 1;
      /*       this.adModal = 1;
      this.adNombre = item.noM_EMPRE;
      this.adId = item.idD_EMPRE;

      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      } */
    },

    /****************************************/
    /* Metodo para cerrar dialogo          */
    /****************************************/
    close() {
      this.dialog = false;
      this.limpiar();
    },
    /****************************************/
    /* Metodo para limpiar campos          */
    /****************************************/
    limpiar() {
      this.id = "";
      this.noM_TRABA = "";
      this.coD_TRABA = "";
      this.validaMensaje = [];
      this.editedIndex = -1;
    }
  }
};
</script>


