<template>
  <!-- align-start : alinea layout al centro-->
  <v-layout align-start>
    <v-flex>
      <!-- Codigo de vuetify-->
      <v-card shaped class="mx-6">
        <!-- ":search": metodo buscar / items: el array donde se encuentran los datos en console () -->
        <v-data-table
          :headers="headers"
          :items="divisiones"
          :search="search"
          sort-by="deS_DIVIS"
          class="elevation-1"
        >
          <template v-slot:top>
            <v-toolbar flat color="white">
              <v-toolbar-title class="blue--text" v-text="nameOpcion"></v-toolbar-title>
              <v-divider class="mx-4" inset vertical></v-divider>
              <!-- v-spacer:spacio-->
              <v-spacer></v-spacer>
              <!-- Buscar / hide-details:ocultardetalles-->
              <v-text-field
                class="text-xs-center"
                v-model="search"
                append-icon="search"
                label="Bsqueda"
                single-line
                hide-details
              ></v-text-field>
              <v-spacer></v-spacer>

              <!-- Modal para Registrar / Actualizar -->
              <v-dialog v-model="dialog" max-width="500px">
                <template v-slot:activator="{ on }">
                  <v-btn color="success" dark class="mb-2" v-on="on">Nuevo Registro</v-btn>
                </template>
                <!-- Modal: Encabezado -->
                <v-card>
                  <v-img
                    class="white--text align-start"
                    height="100px"
                    src="https://cdn.vuetifyjs.com/images/cards/docks.jpg"
                  >
                    <v-card-title>
                      <span class="headline">{{ formTitle }}</span>
                    </v-card-title>
                  </v-img>
                  <!-- Modal: contenido -->
                  <v-card-text>
                    <v-container grid-list-md>
                      <v-row wrap>
                        <v-col cols="6" sm="6" md="6">
                          <v-checkbox color="info" v-model="esT_CONTR" :label="`Contratista?`"></v-checkbox>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                          <!-- Combo -->
                          <v-select
                            v-model="idD_EMPREList"
                            :items="listEmpresasContratistas"
                            label="Seleccione Contratista"
                            v-if="esT_CONTR"
                          ></v-select>
                        </v-col>
                        <v-col cols="12" sm="12" md="12">
                          <v-text-field v-model="deS_PUEST" label="Nombre"></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                          <!-- Combo -->
                          <v-select
                            v-model="idD_PUEST_PADREList"
                            :items="listPuestosPadres"
                            v-if="esT_CONTR==false"
                            label="Seleccione Puesto Padre"
                          ></v-select>
                        </v-col>

                        <!-- Modal que muestra los mensajes de error -->
                        <v-col cols="12" sm="12" md="12" v-show="valida">
                          <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>
                  <!-- Modal: Acciones -->
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                    <v-btn color="blue darken-1" text @click="guardar">Guardar</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>

              <!-- Modal para Activar / Desactivar -->
              <v-dialog v-model="adModal" max-width="290">
                <v-card>
                  <v-card-title class="headline" v-if="adAccion==1">Activar Item?</v-card-title>
                  <v-card-title class="headline" v-if="adAccion==2">Desactivar Item?</v-card-title>
                  <v-card-text>
                    Ests a punto de
                    <span v-if="adAccion==1">Activar</span>
                    <span v-if="adAccion==2">Desactivar</span>
                    el tem {{adNombre}}
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                      style="color:white"
                      color="green darken-1"
                      @click="activarDesactivarCerrar"
                    >Cancelar</v-btn>
                    <v-btn
                      v-if="adAccion==1"
                      style="color:white"
                      color="orange darken-4"
                      @click="m_activarDesactivar"
                    >Activar</v-btn>
                    <v-btn
                      v-if="adAccion==2"
                      style="color:white"
                      color="orange darken-4"
                      @click="m_activarDesactivar"
                    >Desactivar</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>

         <template v-slot:item.esT_REGIS="{ item }">
             <template v-if="item.esT_REGIS">
             <v-icon color="green">check_box</v-icon>
             </template>
              <template v-else>
                 <v-icon>check_box_outline_blank</v-icon>
                </template>
          </template>

          <template v-slot:item.opciones="{ item }">
            <v-icon small class="mr-2" @click="editItem(item)">edit</v-icon>
            <template v-if="item.esT_REGIS">
              <v-icon small @click="activarDesactivarMostrar(2,item)">block</v-icon>
            </template>
            <template v-else>
              <v-icon small @click="activarDesactivarMostrar(1,item)">check</v-icon>
            </template>
          </template>
          <template v-slot:no-data>
            <v-btn color="primary" @click="listar">Reset</v-btn>
          </template>
        </v-data-table>
      </v-card>
      <!-- -->
    </v-flex>
  </v-layout>
</template>


<script>
import axios from "axios";

export default {
  //name: "Puesto Trabajo",
  data: () => {
    return {
      /** Variables de conexion */
      cod_USUAR_Conexion: "",
      idd_EMPRE_Conexion: 0,
      nameOpcion: "Puesto de Trabajo",
      divisiones: [],
      drawer: null,
      dialog: false,
      headers: [
        { text: "Opciones", value: "opciones", sortable: false },
        { text: "Descripcin", value: "deS_PUEST", sortable: true },
        {
          text: "Descripcin",
          value: "tbM_EMPRE.noM_EMPRE_CORTA",
          sortable: true
        },
        {
          text: "Condicin ",
          value: "esT_REGIS",
          sortable: false,
          align: "center"
        }
      ],
      /*Variables Combos*/
      idD_EMPREList: "",
      listEmpresasContratistas: [],
      listPuestosPadres: [],
      idD_PUEST_PADREList: 0,
      /*Variables Puestos Trabajo*/
      idD_PUEST: "",
      coD_PUEST: "",
      deS_PUEST: "",
      idD_EMPRE: "",
      idD_PUEST_PADRE: "",
      coD_USUAR_CREAC: "",
      feC_USUAR_CREAC: "",
      coD_USUAR_MODIF: "",
      feC_USUAR_MODIF: "",
      esT_REGIS: "",
      esT_CONTR: false,

      /*Variables Basicas Formulario*/
      search: "",
      editedIndex: -1,
      id: "",
      nombre: "",
      descripcion: "",
      valida: 0,
      validaMensaje: [],
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: ""
    };
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? "Nuevo " + this.nameOpcion
        : "Actualizar " + this.nameOpcion;
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    this.idd_EMPRE_Conexion = parseInt(localStorage.getItem("codEmpresa"));
    this.cod_USUAR_Conexion = localStorage.getItem("codUsuario");
    this.listar();
    this.m_listarEmpresasContratistas();
    this.m_listarPuestosPadres();
  },

  methods: {
    /****************************************/
    /* Metodo para listar empresas */
    /****************************************/

    m_listarEmpresasContratistas() {
      let me = this;
      var arrayListado = [];
      axios
        .get(
          "api/Empresas/ListarComboExternasPorEmpresas/" +
            this.idd_EMPRE_Conexion
        )
        .then(function(response) {
          arrayListado = response.data;
          arrayListado.map(function(x) {
            me.listEmpresasContratistas.push({
              text: x.noM_EMPRE,
              value: x.idD_EMPRE
            });
          });
          //me.articulos = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    m_listarPuestosPadres() {
      let me = this;
      var arrayListado = [];
      axios
        .get("api/Puestos/ListarComboInternas")
        .then(function(response) {
          arrayListado = response.data;
          arrayListado.map(function(x) {
            me.listPuestosPadres.push({
              text: x.deS_PUEST,
              value: x.idD_PUEST
            });
          });
          //me.articulos = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    //Metodo Listar
    listar() {
      let me = this;
      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      let header = { Authorization: "Bearer" + this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get(
          "api/Puestos/ListarPorEmpresa/" + this.idd_EMPRE_Conexion,
          optionAxios
        )
        .then(function(response) {
          me.divisiones = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    editItem(item) {
      this.idD_PUEST = item.idD_PUEST;
      this.coD_PUEST = item.coD_PUEST;
      this.deS_PUEST = item.deS_PUEST;
      this.idD_EMPRE = item.idD_EMPRE;
      this.idD_PUEST_PADRE = item.idD_PUEST_PADRE;
      this.coD_USUAR_CREAC = item.coD_USUAR_CREAC;
      this.coD_USUAR_MODIF = item.coD_USUAR_MODIF;
      this.editedIndex = 1;
      this.dialog = true;
    },

    deleteItem(item) {
      const index = this.desserts.indexOf(item);
      confirm("Are you sure you want to delete this item?") &&
        this.desserts.splice(index, 1);
    },

    close() {
      this.dialog = false;
      this.limpiar();
    },

    limpiar() {
      this.id = "";
      this.deS_DIVIS = "";
      this.validaMensaje = [];
      this.editedIndex = -1;
    },
    //Metodo Listar
    guardar() {
      //si this.validar es igual a 1 significa que tiene un error y va retornar al modal
      if (this.validar()) {
        return;
      }
      if (this.editedIndex > -1) {
        //Cdigo para editar
        let me = this;
        axios
          .put("api/Puestos/Actualizar", {
            idD_PUEST: me.idD_PUEST,
            coD_PUEST: me.coD_PUEST,
            deS_PUEST: me.deS_PUEST,
            idD_EMPRE: me.idd_EMPRE_Conexion,
            idD_PUEST_PADRE: me.idD_PUEST_PADRE,
            coD_USUAR_CREAC: me.cod_USUAR_Conexion,
            coD_USUAR_MODIF: me.cod_USUAR_Conexion
          })
          .then(function(response) {
            me.close();
            me.listar();
            me.limpiar();
          })
          .catch(function(error) {
            console.log(error);
          });
      } else {
        //Cdigo para guardar

        var me = this;
        var idd_EMPRESaved = 0;
        this.idd_EMPRESaved = me.idd_EMPRE_Conexion;

        if (this.idD_EMPREList != "" && this.idD_EMPREList != me.idd_EMPRE_Conexion)
          this.idd_EMPRESaved = this.idD_EMPREList;
        debugger;

        axios
          .post("api/Puestos/Guardar", {
            coD_PUEST: me.coD_PUEST,
            deS_PUEST: me.deS_PUEST,
            idD_EMPRE: this.idd_EMPRESaved,
            idD_PUEST_PADRE: me.idD_PUEST_PADREList,
            coD_USUAR_CREAC: me.cod_USUAR_Conexion,
            coD_USUAR_MODIF: me.cod_USUAR_Conexion
          })
          .then(function(response) {
            me.close();
            me.listar();
            me.limpiar();
          })
          .catch(function(error) {
            console.log(error);
            console.log(error.request);
            console.log(error.request.response);
          });
      }
    },

    validar() {
      this.valida = 0;
      this.validaMensaje = [];

      if (!this.deS_PUEST) {
        this.validaMensaje.push("Ingrese el nombre del puesto");
      }

      //si es mayor a 0
      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },

    /****************************************/
    /* Metodo para abrir modal para activar o desactivar */
    /****************************************/

    activarDesactivarMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.deS_PUEST;
      this.adId = item.idD_PUEST;

      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },

    /****************************************/
    /* Metodo para activar o desactivar     */
    /****************************************/
    m_activarDesactivar() {
      let me = this;
      axios
        .put("api/Puestos/Estado/" + this.adId, {})
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
        })
        .catch(function(error) {
          console.log(error.request.response);
        });
    },

    activarDesactivarCerrar() {
      this.adModal = 0;
    }
  }
};
</script>