<template>
  <!-- ":search": metodo buscar / items: el array donde se encuentran los datos en console () -->
  <v-data-table
    :headers="headers"
    :items="listPrincipal"
    :search="search"
    group-by="noM_SISTE"
    sort-by="idD_PERFI"
    class="elevation-1"
    loading="true"
  >
   <template v-slot:group.header="{ group, headers, toggle, isOpen }">
            <td :colspan="headers.length">
                <v-btn @click="toggle" small icon :ref="group" :data-open="isOpen">
                    <v-icon v-if="isOpen">mdi-chevron-up</v-icon>
                    <v-icon v-else>mdi-chevron-down</v-icon>
                </v-btn>
                {{ group }}
            </td>
        </template>

    <template v-slot:top>
      <v-toolbar flat color="white">
        <v-toolbar-title class="blue--text">Perfiles</v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <template>
          <v-btn small color="warning" dark class="mb-2" @click="m_Regresar()">Regresar</v-btn>
        </template>
        <!-- v-spacer:spacio-->
        <v-spacer></v-spacer>

        <!-- Buscar / hide-details:ocultardetalles-->
        <v-text-field
          class="text-xs-center"
          v-model="search"
          append-icon="search"
          label="Bsqueda"
          single-line
          hide-details
        ></v-text-field>
        <v-spacer></v-spacer>


      </v-toolbar>
    </template>

    <template v-slot:item.esT_REGIS="{ item }">
      <template v-if="item.esT_REGIS">
        <v-btn text class="gray" @click="m_ActivarDesactivar(item)">
          <v-icon color="blue">check_box</v-icon>
        </v-btn>
      </template>
      <template v-else>
        <v-btn text class="gray" @click="m_ActivarDesactivar(item)">
          <v-icon>check_box_outline_blank</v-icon>
        </v-btn>
      </template>
    </template>
  </v-data-table>
</template>


<script>
import axios from "axios";

export default {
  props: {
    // Simple type validation
    item_Param: String
  },
  //name: "Actividad Econmica",
  data: () => {
    return {
      /** Variables de conexion NO TOCAR*/
      imageDialogo: "",
      cod_USUAR_Conexion: "",
      idd_EMPRE_Conexion: 0,
      listPrincipal: [],

      /* Variables de la pantalla  NO TOCAR*/
      search: "",
      editedIndex: -1,
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: "",
      drawer: null,
      dialog: false,
      loading: true,

      /** Variables del listado general */
      nameOpcion: "Perfil", //Nombre que se muestra en la pantalla
      nameAxios: "Perfiles", //Nombre que se utiliza para buscar el backend

      /** */
      listPrincipal: [],

      headers: [
        { text: "Opcin", value: "noM_PERFI", sortable: true },
        { text: "Grabar", value: "esT_REGIS", sortable: true },
        { text: "", value: "detalle", sortable: false }
        /*     {
          text: "Condicin ",
          value: "esT_REGIS",
          sortable: false,
          align: "center"
        }, 
        { text: "", value: "detalle", sortable: false }*/
      ],
      /* Variables de la lista */
      userName: "",
      idD_PERFI: 0,
      esT_REGIS: false,
      idD_SISTE: 0,
      idD_EMPRE_USUAR: 0
    };
  },

  computed: {
    /****************************************/
    /* Metodo que funcionan dentro de la ventana */
    /****************************************/
    formTitle() {
      return this.editedIndex === -1
        ? "Nuevo " + this.nameOpcion
        : "Actualizar " + this.nameOpcion;
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    /****************************************/
    /* Operaciones que se cargan al iniciar la ventana */
    /****************************************/
    this.idD_SISTE = this.item_Param;

    this.idd_EMPRE_Conexion = parseInt(localStorage.getItem("codEmpresa"));
    this.cod_USUAR_Conexion = localStorage.getItem("codUsuario");
    this.m_ListarPrincipal();
  },

  methods: {
    m_ActivarDesactivar(item) {
    if(!item.esT_REGIS)
    {
        /* Registro Nuevo */

      let me = this;
      let model = {
        idD_PERFI: item.idD_PERFI,
        userName: this.item_Param,
        coD_USUAR_CREAC: me.cod_USUAR_Conexion,
        coD_USUAR_MODIF: me.cod_USUAR_Conexion
      };

      axios
        .post("api/PerfilesPorUsuario/Guardar", model)
        .then(function(response) {
          me.close();
          me.m_ListarPrincipal();
          me.m_limpiar();
        })
        .catch(function(error) {
          console.log(error);
          console.log(error.request.response);
        });
    }
    else{
        /* Eliminamos Registro */
              let me = this;
      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      axios
        .delete("api/PerfilesPorUsuario/Eliminar/" + item.idD_EMPRE_USUAR, optionAxios)
        .then(function(response) {
          me.m_ListarPrincipal();
          me.close();
        })
        .catch(function(error) {
          console.log(error);
          console.log(error.request);
        });
    }

    },

    m_ListarPrincipal() {
      let me = this;
      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      axios
        .get(
          "api/PerfilesPorUsuario/ListarPorUsuario/" + this.item_Param,
          optionAxios
        )
        .then(function(response) {
          me.listPrincipal = response.data;
        })
        .catch(function(error) {
          console.log(error);
        })
        .finally(() => (this.loading = false));
    },

    

    m_Regresar() {
      this.$emit("CerrarDialogo", "prueba");
      this.vContainerSistema = 0;
    },

    /****************************************/
    /* Metodo para cerrar dialogo          */
    /****************************************/
    close() {
      this.dialog = false;
      this.m_limpiar();
    },

    /****************************************/
    /* Metodo para limpiar campos          */
    /****************************************/
    m_limpiar() {
      this.id = "";
      this.validaMensaje = [];
      this.editedIndex = -1;
    },


  }
};
</script>