<template>
  <!-- align-start : alinea layout al centro-->
  <v-layout align-start overflow:auto>
    <v-flex overflow:auto>
      <!-- Codigo de vuetify-->
      <v-card shaped class="mx-6" overflow:auto>
        <!-- ":search": metodo buscar / items: el array donde se encuentran los datos en console () -->
        <v-data-table
          :headers="headers"
          :items="listSistemas"
          :search="search"
          sort-by="nuM_CORRE"
          class="elevation-1"
          v-if="vContainerSistema==0"
          :loading="loading"
          mobile-breakpoint="0"
        >
          <template v-slot:top>
            <v-toolbar flat color="white">
              <v-toolbar-title class="blue--text" v-text="nameOpcion"></v-toolbar-title>

              <v-divider class="mx-4" inset vertical></v-divider>

              <!-- v-spacer:spacio-->
              <v-spacer></v-spacer>
              <!-- Buscar / hide-details:ocultardetalles-->
              <v-text-field
                class="text-xs-center"
                v-model="search"
                append-icon="search"
                label="Bsqueda"
                single-line
                hide-details
              ></v-text-field>
              <v-spacer></v-spacer>

              <Buttons val_Param="Nuevo" @click.native="m_AbrirNuevo" />

              <v-form ref="form" lazy-validation>
                <!-- Modal para Registrar / Actualizar -->
                <v-dialog v-model="dialog" max-width="500px">
                  <!-- Modal: Encabezado -->
                  <v-card>
                    <v-img
                      class="white--text align-start"
                      height="100px"
                      src="https://cdn.vuetifyjs.com/images/cards/docks.jpg"
                    >
                      <v-card-title>
                        <span class="headline">{{ formTitle }}</span>
                      </v-card-title>
                    </v-img>
                    <!-- Modal: contenido -->
                    <v-card-text>
                      <v-container grid-list-md>
                        <v-row wrap>
                          <v-col cols="12" sm="12" md="12">
                            <v-text-field v-model="noM_SISTE" label="Descripcin"></v-text-field>
                          </v-col>
                          <v-col cols="12" sm="12" md="12">
                            <v-text-field v-model="noM_GRUPO" label="Alias"></v-text-field>
                          </v-col>
                          <v-col cols="12" sm="12" md="12">
                            <v-text-field type="number" v-model="nuM_CORRE" label="Correlativo"></v-text-field>
                          </v-col>

                          <!-- Modal que muestra los mensajes de error -->
                          <v-col cols="12" sm="12" md="12" v-show="valida">
                            <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card-text>
                    <!-- Modal: Acciones -->
                    <v-card-actions>
                      <v-spacer></v-spacer>

                      <Buttons val_Param="Cancelar" @click.native="m_close" />
                      <Buttons val_Param="Guardar" @click.native="guardarSistema" />
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-form>
              <!-- Modal para Activar / Desactivar -->
              <v-dialog v-model="adModal" max-width="350">
                <v-card>
                  <v-card-title class="headline" v-if="adAccion==1">Activar Item?</v-card-title>
                  <v-card-title class="headline" v-if="adAccion==2">Desactivar Item?</v-card-title>
                  <v-card-text>
                    Ests a punto de
                    <span v-if="adAccion==1">Activar</span>
                    <span v-if="adAccion==2">Desactivar</span>
                    el tem {{adNombre}}
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <Buttons val_Param="Cancelar" @click.native="m_activarDesactivarCerrar" />
                    <Buttons
                      v-if="adAccion==1"
                      val_Param="Activar"
                      @click.native="m_activarDesactivar"
                    />
                    <Buttons
                      v-if="adAccion==2"
                      val_Param="Desactivar"
                      @click.native="m_activarDesactivar"
                    />
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>

             <template v-slot:item.esT_REGIS="{ item }">
             <template v-if="item.esT_REGIS">
             <v-icon color="green">check_box</v-icon>
             </template>
              <template v-else>
                 <v-icon>check_box_outline_blank</v-icon>
                </template>
          </template>

          <template v-slot:item.opciones="{ item }">
            <v-icon small class="mr-2" @click="editSistema(item)">edit</v-icon>
            <template v-if="item.esT_REGIS">
              <v-icon small @click="activarDesactivarSisMostrar(2,item)">block</v-icon>
            </template>
            <template v-else>
              <v-icon small @click="activarDesactivarSisMostrar(1,item)">check</v-icon>
            </template>
          </template>

          <template v-slot:item.detalle="{ item }">
            <v-btn small color="warning" @click="m_MostrarOpciones(item.idD_SISTE)">Opcin</v-btn>
          </template>
          <template v-slot:item.perfil="{ item }">
            <v-btn small color="success" @click="m_MostrarPerfiles(item.idD_SISTE)">Perfiles</v-btn>
          </template>

          <template v-slot:no-data>
            <v-btn color="primary" @click="listar">Reset</v-btn>
          </template>
        </v-data-table>

        <template v-if="cardOpcionesView==true">
          <Opciones @CerrarDialogo="CerrarDialogo" :item_Param="item_Param" />
        </template>
        <template v-if="cardPerfilesView==true">
          <Perfiles @CerrarDialogo="CerrarDialogo" :item_Param="item_Param" />
        </template>
      </v-card>
      <!-- -->
    </v-flex>
  </v-layout>
</template>


<script>
import axios from "axios";
import Perfiles from "@/components/Seguridad/Perfiles.vue";
import Opciones from "@/components/Seguridad/Opciones.vue";
import Buttons from "@/components/Buttons/Buttons.vue";

export default {
  components: {
    Perfiles,
    Opciones,
    Buttons
  },
  //name: "Actividad Econmica",
  data: () => {
    return {
      //Variables Pantalla
      ////#region
      cod_USUAR_Conexion: "",
      idd_EMPRE_Conexion: 0,
      loading: true,
      item_Param: 0,
      cardPerfilesView: false,
      cardOpcionesView: false,
      drawer: null,
      dialog: false,
      vContainerSistema: 0,
      search: "",
      editedIndex: -1,
      valida: 0,
      validaMensaje: [],
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: "",
      ////#endregion
      /** Variables del listado general */
      nameOpcion: "Sistema",
      selectedFile: null,
      image: "",
      listSistemas: [],
      listOpciones: [],
      listOpcionesPadres: [],
      idD_OPCIOList: 0,

      headers: [
        { text: "Opciones", value: "opciones", sortable: false },
        { text: "Correlativo", value: "nuM_CORRE", sortable: true },
        { text: "Descripcin", value: "noM_SISTE", sortable: true },
        { text: "Alias", value: "noM_GRUPO", sortable: true },
        { text: "Iconos", value: "noM_ICONO", sortable: true },
        {
          text: "Condicin ",
          value: "esT_REGIS",
          sortable: false,
          align: "center"
        },
        { text: "", value: "detalle", sortable: false },
        { text: "", value: "perfil", sortable: false }
      ],

      /*Variables Sistemas*/
      noM_SISTE: "",
      esT_REGIS: "",
      noM_GRUPO: "",
      noM_ICONO: "",
      nuM_CORRE: 0
    };
  },

  computed: {
    /****************************************/
    /* Metodo que funcionan dentro de la ventana */
    /****************************************/
    formTitle() {
      return this.editedIndex === -1
        ? "Nueva " + this.nameOpcion
        : "Actualizar " + this.nameOpcion;
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    /****************************************/
    /* Operaciones que se cargan al iniciar la ventana */
    /****************************************/
    this.idd_EMPRE_Conexion = parseInt(localStorage.getItem("codEmpresa"));
    this.cod_USUAR_Conexion = localStorage.getItem("codUsuario");
    this.listar();
  },

  methods: {
    CerrarDialogo() {
      this.cardOpcionesView = false;
      this.cardPerfilesView = false;
      this.vContainerSistema = 0;
    },

    m_MostrarOpciones(item) {
      this.item_Param = item;
      this.cardOpcionesView = true;
      this.vContainerSistema = 1;
    },

    m_MostrarPerfiles(item) {
      this.item_Param = item;
      this.cardPerfilesView = true;
      this.vContainerSistema = 1;
    },

    mostrarNuevo(id) {
      this.idD_SISTE_OPCIO = id;
      this.listarOpcionesPorSistema(id);
      this.listarOpcionesPadresPorSistema(id);
      this.vContainerSistema = 1;
    },

    m_Regresar() {
      this.vContainerSistema = 0;
    },

    /****************************************/
    /* Metodo que carga las variables cuando selcciono editar */
    /****************************************/
    editSistema(item) {
      this.idD_SISTE = item.idD_SISTE;
      this.noM_SISTE = item.noM_SISTE;
      this.noM_GRUPO = item.noM_GRUPO;
      this.nuM_CORRE = item.nuM_CORRE;
      this.noM_ICONO = item.noM_ICONO;
      this.editedIndex = 1;
      this.dialog = true;
    },

    editOpcion(item) {
      this.idD_SISTE = item.idD_SISTE;
      this.noM_OPCIO = item.noM_OPCIO;
      this.noM_ACTIO = item.noM_ACTIO;
      this.nuM_CORRE = item.nuM_CORRE;
      this.esT_SUBME = item.esT_SUBME;
      this.idD_OPCIO_PADRE = item.idD_OPCIO_PADRE;
      this.editedIndex = 1;
      this.dialog = true;
    },

    /****************************************/
    /* Metodo para listar Empresas       */
    /****************************************/
    listar() {
      let me = this;
      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      let header = { Authorization: "Bearer" + this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("api/Sistemas/Listar", optionAxios)
        .then(function(response) {
          me.listSistemas = response.data;
        })
        .catch(function(error) {
          console.log(error);
        })
        .finally(() => (this.loading = false));
    },

    /****************************************/
    /* Metodo para cerrar dialogo          */
    /****************************************/
    close() {
      this.dialog = false;
      this.m_limpiar();
    },

    /****************************************/
    /* Metodo para limpiar campos          */
    /****************************************/
    m_limpiar() {
      this.id = "";
      this.validaMensaje = [];
      this.editedIndex = -1;
    },

    /****************************************/
    /* Metodo para guardar/actualizar  Sistema     */
    /****************************************/
    guardarSistema() {
      //si this.validar es igual a 1 significa que tiene un error y va retornar al modal
      if (this.validaSistema()) {
        return;
      }
      if (this.editedIndex > -1) {
        let me = this;
        axios
          .put("api/Sistemas/Actualizar", {
            idD_SISTE: me.idD_SISTE,
            noM_SISTE: me.noM_SISTE,
            noM_GRUPO: me.noM_GRUPO,
            nuM_CORRE: parseInt(me.nuM_CORRE),
            noM_ICONO: me.noM_ICONO
          })
          .then(function(response) {
            me.listar();
            me.close();
          })
          .catch(function(error) {
            console.log(error);
            console.log(error.request);
            console.log(error.request.response);
          });
      } else {
        var me = this;
        axios
          .post("api/Sistemas/Guardar", {
            noM_SISTE: me.noM_SISTE,
            noM_GRUPO: me.noM_GRUPO,
            nuM_CORRE: parseInt(me.nuM_CORRE),
            noM_ICONO: me.noM_ICONO
          })
          .then(function(response) {
            me.close();
            me.listar();
            me.m_limpiar();
          })
          .catch(function(error) {
            console.log(error);
            console.log(error.request.response);
          });
      }
    },

    guardar() {
      //si this.validar es igual a 1 significa que tiene un error y va retornar al modal
      if (this.validar()) {
        return;
      }
      if (this.editedIndex > -1) {
        let me = this;
        axios
          .put("api/ActivEconomicas/Actualizar", {
            idD_OPCIO: me.idD_OPCIO,
            idD_SISTE: me.idD_SISTE_OPCIO,
            noM_OPCIO: me.noM_OPCIO,
            noM_ACTIO: me.noM_ACTIO,
            nuM_CORRE: parseInt(me.nuM_CORRE),
            esT_SUBME: me.esT_SUBME,
            idD_OPCIO_PADRE: me.idD_OPCIOList
          })
          .then(function(response) {
            me.listar();
            me.close();
          })
          .catch(function(error) {
            console.log(error);
            console.log(error.request);
            console.log(error.request.response);
          });
      } else {
        var me = this;
        axios
          .post("api/Opciones/Guardar", {
            idD_SISTE: me.idD_SISTE_OPCIO,
            noM_OPCIO: me.noM_OPCIO,
            noM_ACTIO: me.noM_ACTIO,
            nuM_CORRE: parseInt(me.nuM_CORRE),
            esT_SUBME: me.esT_SUBME,
            idD_OPCIO_PADRE: me.idD_OPCIOList
          })
          .then(function(response) {
            me.close();
            me.listar();
            me.m_limpiar();
          })
          .catch(function(error) {
            console.log(error);
            console.log(error.request.response);
          });
      }
    },

    /****************************************/
    /* Metodo para validar antes de guardar */
    /****************************************/
    validaSistema() {
      this.valida = 0;
      this.validaMensaje = [];

      if (!this.noM_SISTE) {
        this.validaMensaje.push("Ingrese el nombre del sistema");
      }

      if (!this.noM_GRUPO) {
        this.validaMensaje.push("Ingrese el nombre del alias");
      }
      if (!this.nuM_CORRE) {
        this.validaMensaje.push("Ingrese el correlativo");
      }

      //si es mayor a 0
      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },

    validar() {
      this.valida = 0;
      this.validaMensaje = [];

      if (!this.noM_OPCIO) {
        this.validaMensaje.push("Ingrese el nombre de la opcin");
      }

      //si es mayor a 0
      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },

    /****************************************/
    /* Metodo para abrir modal para activar o desactivar */
    /****************************************/

    activarDesactivarSisMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.noM_SISTE;
      this.adId = item.idD_SISTE;

      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },

    /****************************************/
    /* Metodo para abrir modal para activar o desactivar */
    /****************************************/

    activarDesactivarMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.noM_OPCIO;
      this.adId = item.idD_OPCIO;

      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },

    /****************************************/
    /* Metodo para activar o desactivar     */
    /****************************************/
    m_activarDesactivarSis() {
      let me = this;
      axios
        .put("api/Sistemas/Estado/" + this.adId, {})
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
          location.reload();
        })
        .catch(function(error) {
          console.log(error.request.response);
        });
    },

    /****************************************/
    /* Metodo para activar o desactivar     */
    /****************************************/
    m_activarDesactivar() {
      let me = this;
      axios
        .put("api/Opciones/Estado/" + this.adId, {})
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
        })
        .catch(function(error) {
          console.log(error.request.response);
        });
    },
    m_AbrirNuevo() {
      this.$refs.form.reset();
      this.dialog = true;
    },
    /****************************************/
    /* Metodo para cerrar modal si no se desea activar/desactivar */
    /****************************************/
    activarDesactivarCerrar() {
      this.adModal = 0;
    },
    /****************************************/
    /* Metodo para cerrar dialogo de Nuevo y Editar */
    /****************************************/
    m_close() {
      this.dialog = false;
      this.m_limpiar();
    }
  }
};
</script>