<template>
  <!-- align-start : alinea layout al centro-->
  <v-data-table
    :headers="headersOpciones"
    :items="listOpciones"
    :search="search"
    group-by="noM_OPCIO_PADRE"
    sort-by="nuM_CORRE"
    class="elevation-1"
    loading="true"
  >
    <template v-slot:group.header="{ group, headers, toggle, isOpen }">
      <td :colspan="headers.length">
        <v-btn @click="toggle" small icon :ref="group" :data-open="isOpen">
          <v-icon v-if="isOpen">mdi-chevron-up</v-icon>
          <v-icon v-else>mdi-chevron-down</v-icon>
        </v-btn>
        {{ group }}
      </td>
    </template>

    <template v-slot:item.opciones="{ item }">
      <v-icon small class="mr-2" @click="editOpcion(item)">edit</v-icon>
      <template v-if="item.esT_REGIS">
        <v-icon small @click="activarDesactivarMostrar(2,item)">block</v-icon>
      </template>
      <template v-else>
        <v-icon small @click="activarDesactivarMostrar(1,item)">check</v-icon>
      </template>
    </template>

    <template v-slot:item.esT_SUBME="{ item }">
      <v-checkbox color="info" v-model="item.esT_SUBME" disabled></v-checkbox>
    </template>

        <template v-slot:item.esT_REGIS="{ item }">
             <template v-if="item.esT_REGIS">
             <v-icon color="green">check_box</v-icon>
             </template>
              <template v-else>
                 <v-icon>check_box_outline_blank</v-icon>
                </template>
          </template>

    <template v-slot:no-data>
      <v-btn color="primary" @click="listar">Reset</v-btn>
    </template>

    <template v-slot:top>
      <v-toolbar flat color="white">
        <v-toolbar-title class="blue--text" v-text="nameOpcionDetalle"></v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <template>
          <v-btn small color="warning" dark class="mb-2" @click="m_Regresar()">Regresar</v-btn>
        </template>
        <!-- v-spacer:spacio-->
        <v-spacer></v-spacer>

        <!-- Buscar / hide-details:ocultardetalles-->
        <v-text-field
          class="text-xs-center"
          v-model="search"
          append-icon="search"
          label="Bsqueda"
          single-line
          hide-details
        ></v-text-field>
        <v-spacer></v-spacer>

        <v-form ref="form" lazy-validation>
          <!-- Modal para Registrar / Actualizar -->
          <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on }">
              <v-btn color="success" dark class="mb-2" v-on="on">Nuevo Registro</v-btn>
            </template>
            <!-- Modal: Encabezado -->
            <v-card>
              <v-img
                class="white--text align-start"
                height="100px"
                src="https://cdn.vuetifyjs.com/images/cards/docks.jpg"
              >
                <v-card-title>
                  <span class="headline">{{ formTitle }}</span>
                </v-card-title>
              </v-img>
              <!-- Modal: contenido -->
              <v-card-text>
                <v-container grid-list-md>
                  <v-row wrap>
                    <v-col cols="12" sm="12" md="12">
                      <input type="checkbox" id="checkbox" v-model="esT_SUBME" />
                      <label for="checkbox">SubMenu?</label>
                    </v-col>
                    <v-col cols="12" sm="12" md="12">
                      <v-text-field
                        v-model="noM_OPCIO"
                        label="Descripcin"
                        :rules="[() => !!noM_OPCIO || 'Opcin es requerido']"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="12" md="12" v-if="esT_SUBME==false">
                      <v-text-field v-model="noM_ACTIO" label="Router"></v-text-field>
                    </v-col>

                    <v-col cols="12" sm="12" md="12">
                      <v-text-field type="number" v-model="nuM_CORRE" label="Correlativo"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="12" md="12" v-if="esT_SUBME==false">
                      <v-select
                        v-model="idD_OPCIOList"
                        :items="listOpcionesPadres"
                        label="Seleccione Opcin Padre"
                      ></v-select>
                    </v-col>
                    <!-- Modal que muestra los mensajes de error -->
                    <v-col cols="12" sm="12" md="12" v-show="valida">
                      <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
              <!-- Modal: Acciones -->
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="btnCancelar" text @click="close">Cancelar</v-btn>
                <v-btn color="btnGuardar" text @click="guardar">Guardar</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-form>
        <!-- Modal para Activar / Desactivar -->
        <v-dialog v-model="adModal" max-width="290">
          <v-card>
            <v-card-title class="headline" v-if="adAccion==1">Activar Item?</v-card-title>
            <v-card-title class="headline" v-if="adAccion==2">Desactivar Item?</v-card-title>
            <v-card-text>
              Ests a punto de
              <span v-if="adAccion==1">Activar</span>
              <span v-if="adAccion==2">Desactivar</span>
              el tem {{adNombre}}
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                style="color:white"
                color="green darken-1"
                @click="activarDesactivarCerrar"
              >Cancelar</v-btn>
              <v-btn
                v-if="adAccion==1"
                style="color:white"
                color="orange darken-4"
                @click="m_activarDesactivar"
              >Activar</v-btn>
              <v-btn
                v-if="adAccion==2"
                style="color:white"
                color="orange darken-4"
                @click="m_activarDesactivar"
              >Desactivar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
  </v-data-table>
</template>


<script>
import axios from "axios";

export default {
  props: {
    // Simple type validation
    item_Param: Number
  },
  //name: "Actividad Econmica",
  data: () => {
    return {
      /** Variables de conexion */
      cod_USUAR_Conexion: "",
      idd_EMPRE_Conexion: 0,
      /** Variables del listado general */
      nameOpcion: "Men",
      nameOpcionDetalle: "Opcin",
      selectedFile: null,
      image: "",
      listOpciones: [],
      listOpcionesPadres: [],
      drawer: null,
      headersOpciones: [
        { text: "Opciones", value: "opciones", sortable: false },
        { text: "Descripcin", value: "noM_OPCIO", sortable: true },
        { text: "Router", value: "noM_ACTIO", sortable: true },
        { text: "Correlativo", value: "nuM_CORRE", sortable: true },
        { text: "Submenu?", value: "esT_SUBME", sortable: true },
        { text: "Opcin Padre", value: "noM_OPCIO_PADRE", sortable: true },
        {
          text: "Condicin ",
          value: "esT_REGIS",
          sortable: false,
          align: "center"
        }
      ],
      headers: [
        { text: "Opciones", value: "opciones", sortable: false },
        { text: "Sistema", value: "tbT_SISTE.noM_SISTE", sortable: true },
        { text: "Descripcin", value: "noM_OPCIO", sortable: true },
        {
          text: "Condicin ",
          value: "esT_REGIS",
          sortable: false,
          align: "center"
        }
      ],
      /*Variables */
      noM_OPCIO: "",
      noM_ACTIO: "",
      nuM_CORRE: 0,
      idD_OPCIO_PADRE: "",
      noM_OPCIO_PADRE: "",
      esT_SUBME: false,
      idD_OPCIOList: 0,

      dialog: false,
      search: "",
      editedIndex: -1,
      valida: 0,
      validaMensaje: [],
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: ""
    };
  },

  computed: {
    /****************************************/
    /* Metodo que funcionan dentro de la ventana */
    /****************************************/
    formTitle() {
      return this.editedIndex === -1
        ? "Nueva " + this.nameOpcion
        : "Actualizar " + this.nameOpcion;
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    this.idD_SISTE_OPCIO = this.item_Param;
    /****************************************/
    /* Operaciones que se cargan al iniciar la ventana */
    /****************************************/
    this.idd_EMPRE_Conexion = parseInt(localStorage.getItem("codEmpresa"));
    this.cod_USUAR_Conexion = localStorage.getItem("codUsuario");

    this.listarOpcionesPorSistema(this.item_Param);
    this.listarOpcionesPadresPorSistema(this.item_Param);
  },

  methods: {
    listarOpcionesPadresPorSistema(idd_Sistema) {
      let me = this;
      var arrayListado = [];
      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      let header = { Authorization: "Bearer" + this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("api/Opciones/ListarPadresPorSistema/" + idd_Sistema, optionAxios)
        .then(function(response) {
          //me.listOpcionesPadres = response.data;
          arrayListado = response.data;
          arrayListado.map(function(x) {
            me.listOpcionesPadres.push({
              text: x.noM_OPCIO,
              value: x.idD_OPCIO
            });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    listarOpcionesPorSistema(idd_Sistema) {
      let me = this;
      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      let header = { Authorization: "Bearer" + this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("api/Opciones/ListarPorSistema/" + idd_Sistema, optionAxios)
        .then(function(response) {
          me.listOpciones = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    /****************************************/
    /* Metodo que carga las variables cuando selcciono editar */
    /****************************************/
    editItem(item) {
      this.idD_ACTIV_ECONO = item.idD_ACTIV_ECONO;
      this.coD_ACTIV_ECONO = item.coD_ACTIV_ECONO;
      this.deS_ACTIV_ECONO = item.deS_ACTIV_ECONO;
      this.coD_USUAR_CREAC = item.coD_USUAR_CREAC;
      this.coD_USUAR_MODIF = item.coD_USUAR_MODIF;
      this.editedIndex = 1;
      this.dialog = true;
    },

    /****************************************/
    /* Metodo para listar Empresas       */
    /****************************************/
    listar() {
      let me = this;
      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      let header = { Authorization: "Bearer" + this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("api/Opciones/Listar", optionAxios)
        .then(function(response) {
          me.listActivEconomica = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    /****************************************/
    /* Metodo para cerrar dialogo          */
    /****************************************/
    close() {
      this.dialog = false;
      this.limpiar();
    },

    /****************************************/
    /* Metodo para limpiar campos          */
    /****************************************/
    limpiar() {
      this.id = "";
      this.coD_ACTIV_ECONO = "";
      this.deS_ACTIV_ECONO = "";
      this.validaMensaje = [];
      this.editedIndex = -1;
    },

    /****************************************/
    /* Metodo para guardar/actualizar       */
    /****************************************/
    guardar() {
      //si this.validar es igual a 1 significa que tiene un error y va retornar al modal
      if (this.validar()) {
        return;
      }
      if (this.editedIndex > -1) {
        let me = this;
        axios
          .put("api/ActivEconomicas/Actualizar", {
            idD_ACTIV_ECONO: me.idD_ACTIV_ECONO,
            coD_ACTIV_ECONO: me.coD_ACTIV_ECONO,
            deS_ACTIV_ECONO: me.deS_ACTIV_ECONO,
            coD_USUAR_CREAC: this.cod_USUAR_Conexion,
            coD_USUAR_MODIF: this.cod_USUAR_Conexion
          })
          .then(function(response) {
            me.listar();
            me.close();
          })
          .catch(function(error) {
            console.log(error);
            console.log(error.request);
            console.log(error.request.response);
          });
      } else {
        //Cdigo para guardar
        /*         var prueba = this.selectedFile;
      var blobUri = 'https://' + 'STORAGE_ACCOUNT' + '.blob.core.windows.net';
      var blobService = AzureStorage.Blob.createBlobServiceWithSas(blobUri, 'SAS_TOKEN');
 */
        var me = this;
        axios
          .post("api/Opciones/Guardar", {
            idD_SISTE: me.idD_SISTE_OPCIO,
            noM_OPCIO: me.noM_OPCIO,
            noM_ACTIO: me.noM_ACTIO,
            nuM_CORRE: parseInt(me.nuM_CORRE),
            esT_SUBME: me.esT_SUBME,
            idD_OPCIO_PADRE: me.idD_OPCIOList
          })
          .then(function(response) {
            me.close();
            me.listar();
            me.limpiar();
          })
          .catch(function(error) {
            console.log(error);
            console.log(error.request.response);
          });
      }
    },

    /****************************************/
    /* Metodo para validar antes de guardar */
    /****************************************/

    validar() {
      var validateForm = this.$refs.form.validate();

      return validateForm == true ? 0 : 1;

      /*   this.valida = 0;
      this.validaMensaje = [];

      if (!this.deS_ACTIV_ECONO) {
        this.validaMensaje.push("Ingrese el nombre de la actividad econmica");
      }

      //si es mayor a 0
      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida; */
    },

    /****************************************/
    /* Metodo para abrir modal para activar o desactivar */
    /****************************************/

    activarDesactivarMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.noM_OPCIO;
      this.adId = item.idD_OPCIO;

      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },

    /****************************************/
    /* Metodo para activar o desactivar     */
    /****************************************/
    m_activarDesactivar() {
      let me = this;
      axios
        .put("api/Opciones/Estado/" + this.adId, {})
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
        })
        .catch(function(error) {
          console.log(error.request.response);
        });
    },
    m_Regresar() {
      this.$emit("CerrarDialogo", "prueba");
      this.vContainerSistema = 0;
    },
    /****************************************/
    /* Metodo para cerrar modal si no se desea activar/desactivar */
    /****************************************/
    activarDesactivarCerrar() {
      this.adModal = 0;
    }
  }
};
</script>