<template>
  <v-container grid-list-sm class="pa-4 white">
    <v-form ref="form" v-model="valid" lazy-validation>
      <!-- Modal: Encabezado -->
      <v-card-title>
        <span class="headline">{{ formTitle }}</span>
      </v-card-title>
      <v-card>
        <v-toolbar color="#90caf9" dark flat>
          <v-tabs v-model="currentItem" fixed-tabs slider-color="white">
            <v-tab v-for="item in items" :key="item" :href="'#tab-' + item">{{ item }}</v-tab>
            <!-- 
                <v-menu v-if="more.length" bottom left>
                  <template v-slot:activator="{ on }">
                    <v-btn text class="align-self-center mr-4" v-on="on">
                      more
                      <v-icon right>mdi-menu-down</v-icon>
                    </v-btn>
                  </template>

                  <v-list class="grey lighten-3">
                    <v-list-item v-for="item in more" :key="item" @click="addItem(item)">{{ item }}</v-list-item>
                  </v-list> 
            </v-menu>-->
          </v-tabs>
        </v-toolbar>
      </v-card>

      <v-tabs-items v-model="currentItem">
        <!--             <v-tab-item v-for="item in items.concat(more)" :key="item" :value="'tab-' + item">
              <v-card flat v-if="item == 'Datos Personales'">
                <v-card-text>
                  <h2>Pinche codigo</h2>
                  {{ text }}
                </v-card-text>
              </v-card>
        </v-tab-item>-->
        <v-tab-item v-for="item in items.concat(more)" :key="item" :value="'tab-' + item">
          <v-card v-if="item == 'Datos Personales'">
            <v-card-text>
              <v-container grid-list-md>
                <v-row wrap>
                  <v-col cols="9" sm="9" md="9">
                    <v-row wrap>
                      <v-col cols="9" sm="9" md="6">
                        <v-checkbox color="info" v-model="esT_CONTR" :label="`Contratista?`"></v-checkbox>
                      </v-col>
                      <v-col cols="9" sm="9" md="6">
                        <v-select
                          v-if="esT_CONTR"
                          v-model="idD_EMPREList"
                          :items="listEmpresasContratistas"
                          label="Seleccione Empresa"
                        ></v-select>
                      </v-col>
                      <!-- <v-col cols="4" sm="4" md="4" class="rowspan"></v-col> -->
                      <v-col cols="9" sm="9" md="6">
                        <v-text-field
                          v-model="noM_APELL_PATER"
                          label="Apellido Paterno"
                          :rules="[() => !!noM_APELL_PATER || 'Apellido Paterno es requerido']"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="9" sm="9" md="6">
                        <v-text-field
                          v-model="noM_APELL_MATER"
                          label="Apellido Materno"
                          :rules="[() => !!noM_APELL_MATER || 'Apellido Materno es requerido']"
                        ></v-text-field>
                      </v-col>
                      <!--  <v-col cols="4" sm="4" md="4"></v-col> -->
                      <v-col cols="9" sm="9" md="6">
                        <v-text-field
                          v-model="noM_TRABA"
                          label="Nombres"
                          :rules="[() => !!noM_TRABA || 'Nombres es requerido']"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="9" sm="9" md="6">
                        <!--                     <v-text-field v-model="esT_SEXOS" label="Sexo"></v-text-field>
                        -->
                        <v-radio-group v-model="esT_SEXOS" :mandatory="false" row>
                          <v-radio label="Masculino" color="blue" value="M"></v-radio>
                          <v-radio label="Femenino" color="orange darken-3" value="F"></v-radio>
                        </v-radio-group>
                      </v-col>
                      <!--  <v-col cols="4" sm="4" md="4"></v-col> -->

                      <v-col cols="9" sm="9" md="6">
                        <v-menu
                          v-model="menu_feC_NACIM_TRABA"
                          :close-on-content-click="false"
                          :nudge-right="40"
                          transition="scale-transition"
                          offset-y
                          min-width="290px"
                        >
                          <template v-slot:activator="{ on }">
                            <v-text-field
                              v-model="feC_NACIM_TRABAdmy"
                              label="Fecha de Ingreso"
                              prepend-icon="event"
                              persistent-hint
                              readonly
                              v-on="on"
                            ></v-text-field>
                          </template>
                          <v-date-picker
                            v-model="feC_NACIM_TRABA"
                            @input="menu_feC_NACIM_TRABA = false"
                          ></v-date-picker>
                        </v-menu>
                      </v-col>
                      <v-col cols="9" sm="9" md="6">
                        <v-text-field v-model="noM_GRUPO_SANGU" label="Grupo Sanguineo"></v-text-field>
                      </v-col>
                      <v-col cols="9" sm="9" md="6">
                        <v-text-field
                          v-model="noM_DIREC_TRABA"
                          label="Direccin"
                          :rules="[() => !!noM_DIREC_TRABA || 'Direccin es requerido']"
                        ></v-text-field>
                      </v-col>
                      <!-- <v-col cols="4" sm="4" md="4"></v-col> -->

                      <v-col cols="9" sm="9" md="6">
                        <v-text-field v-model="nuM_TELEF" label="Telfono"></v-text-field>
                      </v-col>
                      <v-col cols="9" sm="9" md="6">
                        <v-text-field v-model="noM_DIREC_EMAIL" label="Email"></v-text-field>
                      </v-col>
                      <!-- <v-col cols="4" sm="4" md="4"></v-col> -->
                      <v-col cols="9" sm="9" md="6">
                        <v-select
                          v-model="idD_ESTAD_CIVILList"
                          :items="listEstadosCiviles"
                          label="Seleccione Estado Civil"
                          :rules="[v => !!v || 'Estado Civil es requerido']"
                        ></v-select>
                      </v-col>
                      <v-col cols="9" sm="9" md="6">
                        <v-select
                          v-model="idD_GRADO_INSTRList"
                          :items="listGradoInstruccion"
                          label="Seleccione Grado Instruccin"
                          :rules="[v => !!v || 'Grado Instruccin es requerido']"
                        ></v-select>
                      </v-col>
                      <!-- <v-col cols="4" sm="4" md="4"></v-col> -->
                      <v-col cols="9" sm="9" md="6">
                        <v-select
                          v-model="idD_DOCUM_IDENTList"
                          :items="listDocumIdentidad"
                          label="Seleccione Documento Identidad"
                          :rules="[v => !!v || 'Documento Identidad es requerido']"
                        ></v-select>
                      </v-col>
                      <v-col cols="9" sm="9" md="6">
                        <v-text-field
                          v-model="nuM_DOCUM_IDENT"
                          label="Nmero Documento"
                          :rules="[() => !!nuM_DOCUM_IDENT || 'Nmero Documento es requerido']"
                        ></v-text-field>
                      </v-col>
                      <!-- Modal que muestra los mensajes de error -->
                      <!--           <v-col cols="12" sm="12" md="12" v-show="valida">
                  <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
                      </v-col>-->
                    </v-row>
                  </v-col>
                  <v-col cols="3" sm="3" md="3">
                    <v-row wrap>
                      <v-label center>Adjuntar Foto</v-label>
                      <!--            <img 
                      v-bind:src="prefillFoto" 
                      width="50" 
                      height="50" 
                      aspect-ratio="1.7" />-->
                      <picture-input
                        radius="10"
                        ref="pictureInput"
                        @change="onChange"
                        width="200"
                        height="200"
                        margin="16"
                        size="10"
                        buttonClass="success"
                        removeButtonClass="error"
                        :removable="true"
                        :prefill="prefillFoto"
                        :customStrings="{
                            upload: '<h1>Bummer!</h1>',
                            drag: 'Drag a  GIF or GTFO',
                            tap: 'Tap here to select a photo <br>from your gallery', // HTML allowed
                            change: 'Change Photo', // Text only
                            remove: 'Remove Photo', // Text only
                            select: 'Select a Photo', // Text only
                            selected: '<p>Photo successfully selected!</p>', // HTML allowed
                            fileSize: 'The file size exceeds the limit', // Text only
                            fileType: 'This file type is not supported.', // Text only
                            aspect: 'Landscape/Portrait' // Text only
                            }"
                      ></picture-input>
                      <v-label center>Adjuntar Firma</v-label>
                      <picture-input
                        ref="pictureInput"
                        @change="onChangeFirma"
                        width="300"
                        height="150"
                        margin="16"
                        accept="image/jpeg, image/png"
                        size="10"
                        :removable="true"
                        :customStrings="{
        upload: '<h1>Bummer!</h1>',
        drag: 'Drag a  GIF or GTFO'
      }"
                      ></picture-input>
                      <br />
                      <!--           <v-avatar size="10rem" variant="dark" color="indigo">
                        <button @click="$refs.file.click()">
                          <v-icon x-large dark>person</v-icon>
                        </button>
                          <input
                        type="file"
                        ref="file"
                        id="input-image"
                        v-show="false"
                        @change="onFileChange"
                        accept="image/*"
                      />
                      </v-avatar>-->
                    </v-row>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
          </v-card>
          <v-card v-else-if="item == 'Datos Empresa'">
            <v-card-text>
              <v-container grid-list-md>
                <v-row wrap>
                  <v-col cols="12" sm="12" md="12">
                    <v-switch
                      color="blue"
                      v-model="esT_ACTIV"
                      :label="`Estado: ${esT_ACTIV_String}`"
                      inset
                    ></v-switch>
                  </v-col>
                  <v-col cols="4" sm="4" md="4">
                    <v-menu
                      v-model="menu_feC_INGRE_EMPRE"
                      :close-on-content-click="false"
                      :nudge-right="40"
                      transition="scale-transition"
                      offset-y
                      min-width="290px"
                    >
                      <template v-slot:activator="{ on }">
                        <v-text-field
                          v-model="feC_INGRE_EMPREdmy"
                          label="Fecha de Ingreso"
                          prepend-icon="event"
                          readonly
                          v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker
                        v-model="feC_INGRE_EMPRE"
                        @input="menu_feC_INGRE_EMPRE = false"
                      ></v-date-picker>
                    </v-menu>
                    <!--                   <v-text-field v-model="feC_NACIM_TRABA" label="Fecha Ingreso"></v-text-field>
                    -->
                  </v-col>
                  <v-col v-if="esT_CONTR==false" cols="4" sm="4" md="4">
                    <v-select
                      v-model="idD_DIVISList"
                      :items="listDivisiones"
                      :rules="[v => !!v || 'Divisin es requerido']"
                      label="Seleccione Division"
                    ></v-select>
                  </v-col>
                  <v-col v-if="esT_CONTR==false" cols="4" sm="4" md="4">
                    <v-select
                      v-model="idD_NOMINList"
                      :items="listNominas"
                      label="Seleccione Nmina"
                      :rules="[v => !!v || 'Nmina es requerido']"
                    ></v-select>
                  </v-col>
                  <v-col v-if="esT_CONTR==false" cols="4" sm="4" md="4">
                    <v-select
                      v-model="idD_SEDESList"
                      :items="listSedes"
                      label="Seleccione Sede"
                      :rules="[v => !!v || 'Sede es requerido']"
                    ></v-select>
                  </v-col>
                  <v-col v-if="esT_CONTR==false" cols="4" sm="4" md="4">
                    <v-select
                      v-model="idD_AREASList"
                      :items="listAreas"
                      label="Seleccione reas"
                      :rules="[v => !!v || 'rea es requerido']"
                    ></v-select>
                  </v-col>
                  <v-col v-if="esT_CONTR==false" cols="4" sm="4" md="4">
                    <v-select
                      v-model="idD_SECCIList"
                      :items="listSeccion.filter(word => word.idD_AREAS == idD_AREASList)"
                      label="Seleccione Seccin"
                      :rules="[v => !!v || 'Seccin es requerido']"
                    ></v-select>
                  </v-col>
                  <v-col cols="4" sm="4" md="4">
                    <v-select
                      v-model="idD_PUESTList"
                      :items="listPuestos"
                      label="Seleccione Puesto"
                      :rules="[v => !!v || 'Puesto es requerido']"
                    ></v-select>
                  </v-col>
                  <v-col v-if="esT_CONTR==false" cols="4" sm="4" md="4">
                    <v-select
                      v-model="idD_TIPOS_CONTRList"
                      :items="listContratos"
                      label="Seleccione Contrato"
                      :rules="[v => !!v || 'Contrato es requerido']"
                    ></v-select>
                  </v-col>
                  <v-col v-if="esT_CONTR==false" cols="4" sm="4" md="4">
                    <v-select
                      v-model="idD_TURNOList"
                      :items="listTurnos"
                      label="Seleccione Turno"
                      :rules="[v => !!v || 'Turno es requerido']"
                    ></v-select>
                  </v-col>

                  <v-col v-if="esT_CONTR==false && esT_ACTIV==false" cols="4" sm="4" md="4">
                    <v-menu
                      v-model="feC_CESES_TRABA_"
                      :close-on-content-click="false"
                      :nudge-right="40"
                      transition="scale-transition"
                      offset-y
                      min-width="290px"
                    >
                      <template v-slot:activator="{ on }">
                        <v-text-field
                          v-model="date"
                          label="Fecha de Cese"
                          prepend-icon="event"
                          readonly
                          v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker v-model="feC_CESES_TRABA" @input="feC_CESES_TRABA_ = false"></v-date-picker>
                    </v-menu>
                  </v-col>
                  <v-col v-if="esT_CONTR==false && esT_ACTIV==false" cols="4" sm="4" md="4">
                    <v-select
                      v-model="idD_MOTIV_SEPARList"
                      :items="listMotivosSeparacion"
                      label="Seleccione Motivo Separacin"
                      :rules="[v => !!v || 'Motivo de Separacin es requerido']"
                    ></v-select>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
          </v-card>
          <v-card v-else-if="item == 'Videos'">
            <v-card-text>
              <h2>Pinche Videos</h2>
              {{ text }}
            </v-card-text>
          </v-card>
          <v-card flat v-else>
            <v-card-text>
              <h2>{{ item }}</h2>
              {{ text }}
            </v-card-text>
          </v-card>
        </v-tab-item>
      </v-tabs-items>
      <!-- Modal: Acciones
                    Botoneras para Cancelar y Guardar
      -->
      <v-card-actions>
        <v-row wrap>
          <v-col cols="12" sm="12" md="12" v-show="valida">
            <div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
          </v-col>
        </v-row>
        <v-spacer></v-spacer>
        <v-btn color="btnCancelar" class="white--text" @click="ocultarNuevo">Cancelar</v-btn>
        <v-btn color="btnGuardar" class="white--text" v-if="verNuevo==0" @click="guardar">Guardar</v-btn>
        <!--            <v-btn
              color="btnGuardar"
              class="white--text"
              v-if="verNuevo==0"
              @click="guardar"
        >Guardar</v-btn>-->
      </v-card-actions>
    </v-form>
  </v-container>
</template>

<style scoped>
.v-text-field {
  padding-top: 1px;
  font-size: 5px;
}
.col-sm-9 {
  padding: 1px;
  padding-left: 11px;
  padding-right: 11px;
}
.col-sm-6 {
  padding: 1px;
  padding-left: 11px;
  padding-right: 11px;
}
.v-input {
  font-size: 0.9em;
}
.v-label {
  font-size: 0.9em;
}
</style>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>

<script>
/**   */
import axios from "axios";
import DatetimePicker from "vuetify-datetime-picker";
import PictureInput from "vue-picture-input";
import VCalendar from "v-calendar";

import Vue from "vue";
import VueToast from "vue-toast-notification";
import "vue-toast-notification/dist/theme-sugar.css";

Vue.use(VueToast);

export default {
  props: {
    // Simple type validation
    idD_TRABA_Param: Number
  },
  name: "fichaTrabajador",
  data: () => {
    return {
      dataFromServer: undefined,
      prefillFoto: "",
      //date: new Date().toISOString().substr(0, 10),
      valid: true,
      feC_NACIM_TRABA_: false,
      feC_CESES_TRABA_: false,
      feC_INGRE_EMPRE_: false,
      menu_feC_NACIM_TRABA: false,
      menu_feC_INGRE_EMPRE: false,
      modal: false,
      menu2: false,
      // date: new Date().toISOString().substr(0, 10),
      /*   dateFormatted: this.formatDate(new Date().toISOString().substr(0, 10)),
      menu1: false,
      menu2: false,  */
      /** Tabs */
      currentItem: "tab-Web",
      items: ["Datos Personales", "Datos Empresa"],
      more: ["News", "Maps", "Books", "Flights", "Apps"],
      text:
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.",

      /** Variables de conexion */
      cod_USUAR_Conexion: "",
      idd_EMPRE_Conexion: 0,
      /** Variables del listado general */
      nameOpcion: "Ficha Trabajador",
      fichaTrabajador: [],
      /* Cabecera de la tabla principal */
      headers: [
        { text: "Opciones", value: "opciones", sortable: false },
        {
          text: "Tipo Empresa",
          value: "esT_CONTR",
          sortable: false,
          align: "center"
        },
        { text: "Empresa", value: "noM_EMPRE", sortable: true },
        { text: "Cdigo Trabajador", value: "coD_TRABA", sortable: true },
        { text: "Nombre Completo", value: "noM_TRABA", sortable: true },
        {
          text: "Situacin Trabajador ",
          value: "esT_ACTIV",
          sortable: false,
          align: "center"
        }
      ],
      /*Variables Combos*/
      listEmpresasContratistas: [],
      idD_EMPREList: 0,
      listEmpresas: [],
      idD_ESTAD_CIVILList: "",
      listEstadosCiviles: [],
      idD_GRADO_INSTRList: "",
      listGradoInstruccion: [],
      idD_DOCUM_IDENTList: "",
      listDocumIdentidad: [],

      idD_DIVISList: 0,
      listDivisiones: [],

      idD_NOMINList: 0,
      listNominas: [],

      idD_SEDESList: 0,
      listSedes: [],

      idD_AREASList: "",
      listAreas: [],

      idD_SECCIList: "",
      listSeccion: [],

      idD_PUESTList: "",
      listPuestos: [],

      idD_TIPOS_CONTRList: "",
      listContratos: [],

      idD_TURNOList: "",
      listTurnos: [],

      idD_MOTIV_SEPARList: "",
      listMotivosSeparacion: [],

      /** Variables  */
      esT_CONTR: false,
      /*Variables Tabla*/
      idD_TRABA: 0,
      coD_TRABA: "",
      idD_EMPRE: "",
      noM_APELL_PATER: "",
      noM_APELL_MATER: "",
      noM_TRABA: "",
      esT_SEXOS: "M",
      feC_INGRE_EMPRE: new Date().toISOString().substr(0, 10),
      feC_NACIM_TRABA: new Date().toISOString().substr(0, 10),
      feC_CESES_TRABA: "",
      noM_GRUPO_SANGU: "",
      noM_DIREC_TRABA: "",
      nuM_TELEF: "",
      noM_DIREC_EMAIL: "",
      idD_DOCUM_IDENT: "",
      nuM_DOCUM_IDENT: "",
      esT_ACTIV: true,
      esT_REGIS: "",

      /*Variables Basicas Formulario*/
      dialog: false,
      search: "",
      editedIndex: -1,
      valida: 0,
      validaMensaje: [],
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      verNuevo: 0,
      adId: "",
      formHasErrors: false,
      errorMessages: ""
    };
  },
  components: {
    PictureInput
  },
  computed: {
    feC_NACIM_TRABAdmy() {
      return this.formatDate(this.feC_NACIM_TRABA);
    },

    feC_INGRE_EMPREdmy() {
      return this.formatDate(this.feC_INGRE_EMPRE);
    },
    filteredlistSeccion() {
      return this.listSeccion.filter(word => word.idD_AREAS == 2);
    },
    /****************************************/
    /* Metodo que funcionan dentro de la ventana */
    /****************************************/
    formTitle() {
      return this.editedIndex === -1
        ? "Nueva " + this.nameOpcion
        : "Actualizar " + this.nameOpcion;
    },
    esT_ACTIV_String() {
      return this.esT_ACTIV ? "Activo" : "Anulado";
    }
    /*     computedDateFormatted() {
      return this.formatDate(this.date);
    } */
    /*   form () {
      return {
        noM_APELL_PATER: this.noM_APELL_PATER,
        noM_APELL_MATER: this.noM_APELL_MATER,
        noM_TRABA: this.noM_TRABA,
      }
    },   */
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    /*     feC_NACIM_TRABA(val) {
      this.dateFormatted = this.formatDate(this.feC_NACIM_TRABA);
    }, */
    dateYMD(val) {
      this.dateFormatted = this.formatDateYMD(this.date);
    }
  },

  created() {
    /****************************************/
    /* Operaciones que se cargan al iniciar la ventana */
    /****************************************/
    var pruebas = this.$refs.form;
    //debugger;
    //this.prefillFoto = "http://www.austral.com.pe/noticias/20191007122138.jpg";
    this.idd_EMPRE_Conexion = parseInt(localStorage.getItem("codEmpresa"));
    this.cod_USUAR_Conexion = localStorage.getItem("codUsuario");
    this.esT_CONTR = false;
    this.m_ListarComboEmpresasContratistas();
    this.m_ListarComboEstadosCiviles();
    this.m_ListarComboDocuIdentidad();
    this.m_ListarComboGradoInstruccion();
    this.m_ListarComboDivisiones();
