    m_CargarTrabajador: function() {
      let me = this;
      this.idD_TRABA = 23;
      var optionAxios = {
        method: "GET",
        mode: "no-cors",
        headers: {
          "Access-Control-Allow-Origin": "*",
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      axios
        .get("api/Trabajadores/ListarId/" + this.idD_TRABA_Param)
        .then(response => {
          var model = response.data[0];

          this.idD_TRABA = model.idD_TRABA;
          this.idD_DIVISList = model.idD_DIVIS;
          this.idD_NOMINList = model.idD_NOMIN;
          this.idD_SEDESList = model.idD_SEDES;
