          this.feC_NACIM_TRABA = model.feC_NACIM_TRABA;
          this.noM_GRUPO_SANGU = model.noM_GRUPO_SANGU;
          this.noM_DIREC_TRABA = model.noM_DIREC_TRABA;
          this.nuM_TELEF = model.nuM_TELEF;
          this.noM_DIREC_EMAIL = model.noM_DIREC_EMAIL;
