    addItem(item) {
      const removed = this.items.splice(0, 1);
      this.items.push(...this.more.splice(this.more.indexOf(item), 1));
      this.more.push(...removed);
      this.$nextTick(() => {
        this.currentItem = "tab-" + item;
      });
    },
    /****************************************/
    /*Muestra y oculta vista de nuevo Registro*/
    /****************************************/
    mostrarNuevo() {
      this.verNuevo = 1;
    },

    ocultarNuevo() {
      this.$emit("ocultarNuevo", "prueba");
      this.verNuevo = 0;
      this.limpiar();
    },

