    listar() {
      let me = this;

      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      let header = { Authorization: "Bearer" + this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("api/trabajadores/Listar", optionAxios)
        .then(function(response) {
          me.fichaTrabajador = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    validar() {
      this.formHasErrors = false;

      var validateForm = this.$refs.form.validate();

      return validateForm == true ? 0 : 1;

      /*       Object.keys(this.form).forEach(f => {
      if (!this.form[f]) this.formHasErrors = true;
        var prueba = this.$refs[f];
        debugger;
        this.$refs[f].validate(true);
      });  */

      this.valida = 0;
      this.validaMensaje = [];

      if (!this.noM_APELL_PATER) {
        this.validaMensaje.push("Ingrese el apellido paterno");
      }

      if (!this.noM_APELL_MATER) {
        this.validaMensaje.push("Ingrese el apellido materno");
      }

      if (!this.noM_TRABA) {
        this.validaMensaje.push("Ingrese el nombre");
      }

      //si es mayor a 0
      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },

    /****************************************/
    /* Metodo para guardar/actualizar       */
    /****************************************/
    guardar() {
      //si this.validar es igual a 1 significa que tiene un error y va retornar al modal
      if (this.validar()) {
        return;
      }
      if (this.editedIndex > -1) {
        let me = this;
        let model = {
          idD_TRABA: this.idD_TRABA,
          idD_EMPRE: this.esT_CONTR
            ? this.idD_EMPREList
            : me.idd_EMPRE_Conexion,
          coD_TRABA: me.coD_TRABA,
          idD_DIVIS: this.esT_CONTR ? 0 : me.idD_DIVISList,
          idD_NOMIN: this.esT_CONTR ? 0 : me.idD_NOMINList,
          idD_SEDES: this.esT_CONTR ? 0 : me.idD_SEDESList,
          idD_AREAS: this.esT_CONTR ? 0 : me.idD_AREASList,
          idD_SECCI: this.esT_CONTR ? 0 : me.idD_SECCIList,
