          idD_TURNO: this.esT_CONTR ? 0 : me.idD_TURNOList,
          feC_INGRE_EMPRE: me.feC_INGRE_EMPRE,
          feC_CESES_TRABA:
            this.esT_ACTIV && this.esT_CONTR == false
              ? null
              : me.feC_CESES_TRABA,
          idD_MOTIV_SEPAR:
            this.esT_ACTIV && this.esT_CONTR == false
              ? 0
              : me.idD_MOTIV_SEPARList,
          noM_APELL_PATER: me.noM_APELL_PATER,
          noM_APELL_MATER: me.noM_APELL_MATER,
          noM_TRABA: me.noM_TRABA,
          esT_SEXOS: me.esT_SEXOS,
          feC_NACIM_TRABA: me.feC_NACIM_TRABA,
          noM_GRUPO_SANGU: me.noM_GRUPO_SANGU,
          noM_DIREC_TRABA: me.noM_DIREC_TRABA,
          nuM_TELEF: me.nuM_TELEF,
          noM_DIREC_EMAIL: me.noM_DIREC_EMAIL,
          idD_DOCUM_IDENT: me.idD_DOCUM_IDENTList,
          nuM_DOCUM_IDENT: me.nuM_DOCUM_IDENT,
          coD_USUAR_CREAC: me.cod_USUAR_Conexion,
          coD_USUAR_MODIF: me.cod_USUAR_Conexion,
          noM_RUTAS_FOTOS: this.noM_RUTAS_FOTOS,
          noM_RUTAS_FIRMA: this.noM_RUTAS_FIRMA
        };
        debugger;
        axios
          .put("api/Trabajadores/Actualizar", model)
          /*    .then(function(response) { */
          .then(response => {
            /*          me.listar();
            me.close(); */
            Vue.$toast.open({
              message: "Registro actualizado correctamente",
              type: "default",
              position: "top",
              duration: 3000,
              queue: false,
              // all other options
            });
            this.$emit("ocultarNuevo", "prueba");
            /*      this.verNuevo = 0;
            this.limpiar();        */
          })
          .catch(function(error) {
              Vue.$toast.open({
              message: error,
              type: "error",
              position: "top",
              duration: 3000,
              queue: false,
              // all other options
            });
            console.log(error);
            console.log(error.request);
            console.log(error.request.response);
          });
      } else {
        //Cdigo para guardar
        var me = this;

        let model = {
          idD_EMPRE: this.esT_CONTR
            ? this.idD_EMPREList
            : me.idd_EMPRE_Conexion,
          coD_TRABA: me.coD_TRABA,
          idD_DIVIS: this.esT_CONTR ? 0 : me.idD_DIVISList,
          idD_NOMIN: this.esT_CONTR ? 0 : me.idD_NOMINList,
          idD_SEDES: this.esT_CONTR ? 0 : me.idD_SEDESList,
          idD_AREAS: this.esT_CONTR ? 0 : me.idD_AREASList,
          idD_SECCI: this.esT_CONTR ? 0 : me.idD_SECCIList,
          idD_PUEST: me.idD_PUESTList,
          idD_TIPOS_CONTR: this.esT_CONTR ? 0 : me.idD_TIPOS_CONTRList,
          idD_ESTAD_CIVIL: me.idD_ESTAD_CIVILList,
          idD_GRADO_INSTR: me.idD_GRADO_INSTRList,
          idD_TURNO: this.esT_CONTR ? 0 : me.idD_TURNOList,
          feC_INGRE_EMPRE: me.feC_INGRE_EMPRE,
          feC_CESES_TRABA:
            this.esT_ACTIV && this.esT_CONTR == false
              ? null
              : me.feC_CESES_TRABA,
          idD_MOTIV_SEPAR:
            this.esT_ACTIV && this.esT_CONTR == false
              ? 0
              : me.idD_MOTIV_SEPARList,
          noM_APELL_PATER: me.noM_APELL_PATER,
          noM_APELL_MATER: me.noM_APELL_MATER,
          noM_TRABA: me.noM_TRABA,
          esT_SEXOS: me.esT_SEXOS,
          feC_NACIM_TRABA: this.formatDateYMD(this.feC_NACIM_TRABAdmy),
          noM_GRUPO_SANGU: me.noM_GRUPO_SANGU,
          noM_DIREC_TRABA: me.noM_DIREC_TRABA,
          nuM_TELEF: me.nuM_TELEF,
          noM_DIREC_EMAIL: me.noM_DIREC_EMAIL,
          idD_DOCUM_IDENT: me.idD_DOCUM_IDENTList,
          nuM_DOCUM_IDENT: me.nuM_DOCUM_IDENT,
          coD_USUAR_CREAC: me.cod_USUAR_Conexion,
          coD_USUAR_MODIF: me.cod_USUAR_Conexion,
          noM_RUTAS_FOTOS: this.noM_RUTAS_FOTOS,
          noM_RUTAS_FIRMA: this.noM_RUTAS_FIRMA
        };
        debugger;
        axios
          .post("api/Trabajadores/Guardar", model)
          .then(function(response) {
            //me.close();
            //me.listar();
            //me.limpiar();
            this.$emit("ocultarNuevo", "prueba");
            this.verNuevo = 0;
            this.limpiar();
          })
          .catch(function(error) {
            console.log(error);
            console.log(error.request.response);
          });
      }
    },

    /****************************************/
    /* Metodo para cerrar dialogo          */
    /****************************************/
    close() {
      this.dialog = false;
      this.limpiar();
    },
    /****************************************/
    /* Metodo para limpiar campos          */
    /****************************************/
    limpiar() {
      this.id = "";
      this.noM_TRABA = "";
      this.coD_TRABA = "";
      this.validaMensaje = [];
      this.editedIndex = -1;
    }
  }
};
