<template>
  <!-- align-start : alinea layout al centro-->
  <v-layout align-start>
    <v-flex>
      <!-- Codigo de vuetify-->
      <v-card shaped class="mx-6">
        <!-- ":search": metodo buscar / items: el array donde se encuentran los datos en console () -->
        <v-data-table
          :headers="headers"
          :items="usuarios"
          :search="search"
          :loading="loading"
          sort-by="calories"
          class="elevation-1"
          v-if="cardUsuariosView==true"
        >
          <template v-slot:top>
            <v-toolbar flat color="white">
              <v-toolbar-title class="blue--text">Usuarios</v-toolbar-title>
              <v-divider class="mx-4" inset vertical></v-divider>
              <!-- v-spacer:spacio-->
              <v-spacer></v-spacer>
              <!-- Buscar / hide-details:ocultardetalles-->
              <v-text-field
                class="text-xs-center"
                v-model="search"
                append-icon="search"
                label="Bsqueda"
                single-line
                hide-details
              ></v-text-field>
              <v-spacer></v-spacer>
                 
              <Buttons val_Param="Nuevo" @click.native="m_AbrirNuevo()" />
              <v-form ref="form" lazy-validation>
                <!-- Modal para Registrar / Actualizar -->
                <v-dialog v-model="dialog" max-width="500px">
  
                  <v-card>
                    <v-img
                      class="white--text align-start"
                      height="100px"
                      src="https://cdn.vuetifyjs.com/images/cards/docks.jpg"
                    >
                      <v-card-title>
                        <span class="headline">{{ formTitle }}</span>
                      </v-card-title>
                    </v-img>
                    <!-- Modal: contenido -->
                    <v-card-text>
                      <v-container grid-list-md>
                        <v-row wrap>
                          <v-col cols="6" sm="6" md="6">
                            <v-text-field
                              v-model="userName"
                              :rules="[() => !!userName || 'Usuario es requerido']"
                              label="Usuario"
                            ></v-text-field>
                          </v-col>
                          <v-col cols="6" sm="6" md="6">
                            <v-text-field
                              v-model="nombre_Usuario"
                              :rules="[() => !!nombre_Usuario || 'Nombre Usuario es requerido']"
                              label="Nombre Usuario"
                            ></v-text-field>
                          </v-col>
                          <v-col cols="6" sm="6" md="6">
                            <v-text-field
                              v-model="passwordHash"
                              :rules="[() => !!passwordHash || 'Password es requerido']"
                              type="Password"
                              label="Password"
                            ></v-text-field>
                          </v-col>
        
                        </v-row>
                      </v-container>
                    </v-card-text>
                    <!-- Modal: Acciones -->
                    <v-card-actions>
                      <v-spacer></v-spacer>
                     <Buttons val_Param="Cancelar" @click.native="m_close" />
                      <Buttons val_Param="Guardar" @click.native="m_guardar" />
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-form>
              <!-- Modal para Activar / Desactivar -->
              <v-dialog v-model="adModal" max-width="400">
                <v-card>
                  <v-card-title class="headline" v-if="adAccion==1">Activar Item?</v-card-title>
                  <v-card-title class="headline" v-if="adAccion==2">Desactivar Item?</v-card-title>
                  <v-card-text>
                    Ests a punto de
                    <span v-if="adAccion==1">Activar</span>
                    <span v-if="adAccion==2">Desactivar</span>
                    el tem {{adNombre}}
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                     <Buttons val_Param="Cancelar" @click.native="m_activarDesactivarCerrar()"></Buttons>
                    <Buttons v-if="adAccion==1" val_Param="Activar" @click.native="m_activarDesactivar()"></Buttons>
                    <Buttons v-if="adAccion==2" val_Param="Desactivar" @click.native="m_activarDesactivar()"></Buttons>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>

          <template v-slot:item.noM_RUTAS_LOGOS_View="{ item }">
            <img v-bind:src="item.noM_RUTAS_LOGOS_View" width="100" aspect-ratio="1.7" />
          </template>

          <template v-slot:item.isEnabled="{ item }">
            <v-simple-checkbox v-model="item.isEnabled" disabled></v-simple-checkbox>
          </template>

          <template v-slot:item.opciones="{ item }">
            <v-icon small class="mr-2" @click="editItem(item)">edit</v-icon>
            <template v-if="item.isEnabled">
              <v-icon small @click="activarDesactivarMostrar(2,item)">block</v-icon>
            </template>
            <template v-else>
              <v-icon small @click="activarDesactivarMostrar(1,item)">check</v-icon>
            </template>
          </template>
          <template v-slot:item.detalle="{ item }">
            <template>
              <v-btn small color="warning" dark  @click="m_MostrarAccesos(item.userName)">Accesos</v-btn>
            </template>
            
          </template>
                 <template v-slot:item.perfil="{ item }">
              <v-btn small color="green" dark @click="m_MostrarViewPerfiles(item.userName)">Perfil</v-btn>
            </template>
          <template v-slot:no-data>
            <v-btn color="primary" @click="listar">Reset</v-btn>
          </template>
        </v-data-table>


        <template v-if="cardPerfilesView==true">
          <PerfilesPorUsuario @CerrarDialogo="CerrarDialogo" :item_Param="item_Param" />
        </template>

        <template v-if="cardEmpresasView==true">
          <EmpresasPorUsuario @CerrarEmpresas="CerrarEmpresas" :userName_Param="userName_Param" />
        </template>
      </v-card>
      <!-- -->
    </v-flex>
  </v-layout>
</template>


<script>
import axios from "axios";
import Buttons from "@/components/Buttons/Buttons.vue";
import EmpresasPorUsuario from "@/components/Seguridad/EmpresasPorUsuario.vue";
import PerfilesPorUsuario from "@/components/Seguridad/PerfilesPorUsuario.vue";

export default {
  name: "Usuarios",
  components: {
    EmpresasPorUsuario,
    PerfilesPorUsuario,
    Buttons
  },
  data: () => {
    return {
      /** Variables de conexion NO TOCAR*/
      imageDialogo: "",
      cod_USUAR_Conexion: "",
      idd_EMPRE_Conexion: 0,
      listPrincipal: [],

      /* Variables de la pantalla  NO TOCAR*/
      search: "",
      editedIndex: -1,
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: "",
      drawer: null,
      dialog: false,
      loading: true,

      /** Variables del listado general */
      selectedFile: null,
      image: "",
      usuarios: [],
      drawer: null,
      cardUsuariosView: true,
      cardPerfilesView: false,
      cardEmpresasView: false,
      item_Param: "",
      headers: [
        { text: "Opciones", value: "opciones", sortable: false },
        { text: "Cdigo", value: "userName", sortable: true },
        { text: "Nombre", value: "nombre_Usuario", sortable: true },
        {
          text: "Condicin ",
          value: "isEnabled",
          sortable: false,
          align: "center"
        },
        { text: "", value: "detalle", sortable: false },
        { text: "", value: "perfil", sortable: false }
      ],

      /*Variables */
      userName: "",
      nombre_Usuario: "",
      passwordHash: ""
    };
  },

  computed: {
    /****************************************/
    /* Metodo que funcionan dentro de la ventana */
    /****************************************/
    formTitle() {
      return this.editedIndex === -1 ? "Nueva Empresa" : "Actualizar Empresa";
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    /****************************************/
    /* Operaciones que se cargan al iniciar la ventana */
    /****************************************/
    this.idd_EMPRE_Conexion = parseInt(localStorage.getItem("codEmpresa"));
    this.cod_USUAR_Conexion = localStorage.getItem("codUsuario");
    this.iddCuenta_Conexion = localStorage.getItem("iddCuenta");
    this.listar();
  },

  methods: {
    m_MostrarViewPerfiles(item) {
        this.cardUsuariosView = false;
        this.cardPerfilesView = false;
      this.item_Param = item;
      this.cardPerfilesView = true;

    },

    m_MostrarAccesos(item) {
      this.userName_Param = item;
      this.cardUsuariosView = false;
      this.cardEmpresasView = true;
    },

    CerrarDialogo() {
      this.cardPerfilesView = false;
      this.cardEmpresasView = false;

      this.cardUsuariosView = true;
    },

    CerrarEmpresas() {
    this.cardPerfilesView = false;
      this.cardEmpresasView = false;

      this.cardUsuariosView = true;
    },

    onFileSelected(event) {
      var file = this.$refs.file.files[0];

      let reader = new FileReader();
      reader.onload = event => {
        this.noM_RUTAS_LOGOS = event.target.result;
      };
      reader.readAsDataURL(file);
    },

    /****************************************/
    /* Metodo que carga las variables cuando selcciono editar */
    /****************************************/
    editItem(item) {
      this.idD_EMPRE = item.idD_EMPRE;

      this.editedIndex = 1;
      this.dialog = true;
    },

    /****************************************/
    /* Metodo para listar Empresas       */
    /****************************************/
    listar() {
      let me = this;

      var optionAxios = {
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + this.$store.state.token
        }
      };
      axios
        .get("api/Usuarios/Listar", optionAxios)
        .then(function(response) {
          me.usuarios = response.data;
        })
        .catch(function(error) {
          console.log(error);
          console.log(error.request.response);
        })      
        .finally(() => (this.loading = false));
    },

    /****************************************/
    /* Metodo para cerrar dialogo          */
    /****************************************/
    close() {
      this.dialog = false;
      this.m_limpiar();
    },

    /****************************************/
    /* Metodo para limpiar campos          */
    /****************************************/
    m_limpiar() {
      this.id = "";
      this.noM_EMPRE = "";
      this.noM_EMPRE_CORTA = "";
      this.validaMensaje = [];
      this.editedIndex = -1;
    },

    /****************************************/
    /* Metodo para guardar/actualizar       */
    /****************************************/
    guardar() {
      //si this.validar es igual a 1 significa que tiene un error y va retornar al modal
      if (this.validar()) {
        return;
      }
      if (this.editedIndex > -1) {
        let me = this;
        axios
          .put("api/Usuarios/Actualizar", {
            idD_EMPRE: me.idD_EMPRE,
            coD_EMPRE: me.coD_EMPRE
          })
          .then(function(response) {
            me.listar();
            me.close();
          })
          .catch(function(error) {
            console.log(error);
            console.log(error.request);
            console.log(error.request.response);
          });
      } else {
        let me = this;
        axios
          .post("api/Usuarios/Guardar", {
            userName: me.userName,
            nombre_Usuario: me.nombre_Usuario,
            passwordHash: me.passwordHash,
            idD_CUENT: me.iddCuenta_Conexion
          })
          .then(function(response) {
            me.close();
            me.listar();
            me.m_limpiar();
          })
          .catch(function(error) {
            console.log(error);
            console.log(error.request.response);
          });
      }
    },

    /****************************************/
    /* Metodo para validar antes de guardar */
    /****************************************/

    validar() {
      var validateForm = this.$refs.form.validate();
      return validateForm == true ? 0 : 1;
    },

    /****************************************/
    /* Metodo para abrir modal para activar o desactivar */
    /****************************************/

    activarDesactivarMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.noM_EMPRE;
      this.adId = item.idD_EMPRE;

      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },

    /****************************************/
    /* Metodo para activar o desactivar     */
    /****************************************/
    m_activarDesactivar() {
      let me = this;
      axios
        .put("api/Empresas/Estado/" + this.adId, {})
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
        })
        .catch(function(error) {
          console.log(error.request.response);
        });
    },

    /****************************************/
    /* Metodo para cerrar modal si no se desea activar/desactivar */
    /****************************************/
    activarDesactivarCerrar() {
      this.adModal = 0;
    },
   /************************************************/
    /* Metodo para cerrar Modal de Activar/Desactivar */
    /************************************************/   
    m_activarDesactivarCerrar() {
      this.adModal = 0;
    },        
        /************************************************/
    /* Metodo para abrir pantalla de nuevo registro */
    /************************************************/    
    m_AbrirNuevo() {
      this.$refs.form.reset();
      this.dialog = true;
    },
    /****************************************/
    /* Metodo para cerrar dialogo de Nuevo y Editar */
    /****************************************/
    m_close() {
      this.dialog = false;
      this.m_limpiar();
    }    
  }
};
</script>