import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify';
import axios from 'axios';


Vue.config.productionTip = false
axios.defaults.baseURL = 'https://mypersac.azurewebsites.net/'


Vue.prototype.$imageDialog = "https://cdn.vuetifyjs.com/images/cards/docks.jpg"

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
