import Vue from 'vue'
import Vuex from 'vuex'
import decode from 'jwt-decode'
import router from '../router/index'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: null,
    usuario: null,
    idd_EMPRE: null,
  },
  mutations: {
    setToken(state, token) {
      state.token = token;
    },
    setUsuario(state, usuario) {
      state.usuario = usuario;

    },
  },
  actions: {
    guardarToken({ commit }, token) {
      commit("setToken", token)
      commit("setUsuario", decode(token))
      localStorage.setItem("token", token)
    
      var decodes = decode(token)
      localStorage.setItem("codEmpresa", decodes.typ);
      localStorage.setItem("nomEmpresa", decodes.website);
      localStorage.setItem("codUsuario", decodes.sub);
      localStorage.setItem("nomUsuario", decodes.nameid);
      localStorage.setItem("iddCuenta", decodes.nonce);
    },

    autoLogin({ commit }) {
      let token = localStorage.getItem("token")
      if (token) {
        commit("setToken", token)
        commit("setUsuario", decode(token))
      }
    },  

    salir({ commit }) {
      commit("setToken", null)
      commit("setUsuario", null)
      localStorage.removeItem("token")
      localStorage.removeItem("codEmpresa")
      localStorage.removeItem("nomEmpresa")
      localStorage.removeItem("codUsuario")
      localStorage.removeItem("nomUsuario")
      router.push({ name: 'login' })
    }
  },
  modules: {
  }
})
