<template>
  <!-- align-start : alinea layout al centro-->
  <v-layout align-start>
    <v-flex>
      <v-card class="mx-6">
        <!-- ":search": metodo buscar / items: el array donde se encuentran los datos en console () -->
        <v-data-table
          :headers="headers"
          :items="listPrincipal"
          :search="search"
          :loading="loading"
          sort-by="deS_TIPOS_CONTR"
          class="elevation-1"
          id="PrubasTable"
        >
          <template v-slot:top>
            <v-toolbar flat color="white">
              <v-toolbar-title
                class="blue--text"
                v-text="nameOpcion"
              ></v-toolbar-title>
              <v-divider class="mx-4" inset vertical></v-divider>
              <!-- v-spacer:spacio-->
              <v-spacer></v-spacer>
              <!-- Buscar / hide-details:ocultardetalles-->
              <v-text-field
                class="text-xs-center"
                v-model="search"
                append-icon="search"
                label="Bsqueda"
                single-line
                hide-details
              ></v-text-field>
              <v-spacer></v-spacer>
              <Buttons val_Param="Nuevo" @click.native="m_AbrirNuevo()" />
              <!-- Codigo de vuetify-->
              <v-form ref="form" lazy-validation>
                <!-- Dialogo para Registrar / Actualizar -->
                <v-dialog v-model="dialog" max-width="500px">
                  <!-- Modal: Encabezado -->
                  <v-card>
                    <v-img
                      class="white--text align-start"
                      height="100px"
                      v-bind:src="imageDialogo"
                    >
                      <v-card-title>
                        <span class="headline">{{ formTitle }}</span>
                      </v-card-title>
                    </v-img>
                    <!-- Modal: contenido -->
                    <v-card-text>
                      <v-container grid-list-md>
                        <v-row wrap>
                          <v-col cols="12" sm="12" md="12">
                            <v-text-field
                              v-model="deS_TIPOS_CONTR"
                              :rules="[
                              v => !!v || 'Descripcin es requerido',
                              v => (v && v.length <= 100) || 'Nombre de Contrato debe ser menor a 100 caracteres',
                            ]"    
                               :counter="100"                    
                              label="Nombre"
                            ></v-text-field>
                          </v-col>

                        </v-row>
                      </v-container>
                    </v-card-text>
                    <!-- Modal: Acciones -->
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <Buttons val_Param="Cancelar" @click.native="m_close" />
                      <Buttons val_Param="Guardar" @click.native="m_guardar" />
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-form>
              <!-- Dialogo para Activar o Desactivar el registro -->
              <v-dialog v-model="adModal" max-width="400">
                <v-card>
                  <v-card-title class="headline" v-if="adAccion == 1"
                    >Activar Item?</v-card-title
                  >
                  <v-card-title class="headline" v-if="adAccion == 2"
                    >Desactivar Item?</v-card-title
                  >
                  <v-card-text>
                    Ests a punto de
                    <span v-if="adAccion == 1">Activar</span>
                    <span v-if="adAccion == 2">Desactivar</span>
                    el tem {{ adNombre }}
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <Buttons
                      val_Param="Cancelar"
                      @click.native="m_activarDesactivarCerrar()"
                    ></Buttons>
                    <Buttons
                      v-if="adAccion == 1"
                      val_Param="Activar"
                      @click.native="m_activarDesactivar()"
                    ></Buttons>
                    <Buttons
                      v-if="adAccion == 2"
                      val_Param="Desactivar"
                      @click.native="m_activarDesactivar()"
                    ></Buttons>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>

          <template v-slot:item.esT_REGIS="{ item }">
            <!-- Paso el parametro del registro -->
            <Icons
              v-bind:val_Param="item.esT_REGIS"
              small
              @click.native="m_activarDesactivarMostrar(item)"
            />
          </template>

          <template v-slot:item.opciones="{ item }">
            <v-icon small class="mr-2" @click="m_editItem(item)">edit</v-icon>
          </template>
          <template v-slot:no-data>
            <Buttons val_Param="Refrescar" @click.native="m_ListarPrincipal()" ></Buttons>
          </template>
        </v-data-table>
      </v-card>

      <!-- -->
    </v-flex>
  </v-layout>
</template>

<script>
import axios from "axios";
import Buttons from "@/components/Buttons/Buttons.vue";
import Icons from "@/components/Buttons/Icons.vue";

export default {
  name: "TiposContratos",
  components: {
    Buttons,
    Icons,
  },
  data: () => {
    return {
      /** Variables de conexion NO TOCAR*/
      imageDialogo: "",
      cod_USUAR_Conexion: "",
      idd_EMPRE_Conexion: 0,
      listPrincipal: [],

      /* Variables de la pantalla  NO TOCAR*/
      search: "",
      editedIndex: -1,
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: "",
      drawer: null,
      dialog: false,
      loading: true,

      /* Nombres de la opcin y el controller BackEnd */
      nameOpcion: "Tipos de Contrato", //Nombre que se muestra en la pantalla
      nameAxios: "TiposContratos", //Nombre que se utiliza para buscar el backend

      /** Columnas de la tabla */
      headers: [
        {
          text: "Condicin ",
          value: "esT_REGIS",
          sortable: false,
          align: "center",
        },
        { text: "Descripcin", value: "deS_TIPOS_CONTR", sortable: true },
        { text: "Opciones", value: "opciones", sortable: false,align: "center" },
      ],

      /*Variables para la entidad a tratar*/
      idD_TIPOS_CONTR: "",
      coD_TIPOS_CONTR: "",
      deS_TIPOS_CONTR: "",
      coD_USUAR_CREAC: "",
      feC_USUAR_CREAC: "",
      coD_USUAR_MODIF: "",
      feC_USUAR_MODIF: "",
      esT_REGIS: "",
    };
  },

  computed: {
    /** Obtiene el nombre para nuevo o actualizar */
    formTitle() {
      return this.editedIndex === -1
        ? "Nueva " + this.nameOpcion
        : "Actualizar " + this.nameOpcion;
    },
  },

  watch: {
    dialog(val) {
      val || this.m_close();
    },
  },

  created() {
    //Created es el metodo que carga al iniciar el Vue
    /** Cargamos variables de conexin */
    this.idd_EMPRE_Conexion = parseInt(localStorage.getItem("codEmpresa"));
    this.cod_USUAR_Conexion = localStorage.getItem("codUsuario");
    this.imageDialogo = this.$imageDialog;
    this.m_ListarPrincipal();
  },

  methods: {
    /****************************************/
    /* Metodo de Carga Principal del listado */
    /****************************************/
    m_ListarPrincipal() {
      let me = this;
      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
      };

      axios
        .get(
          "api/" +
            this.nameAxios +
            "/ListarPorEmpresa/" +
            this.idd_EMPRE_Conexion,
          optionAxios
        )
        .then(function(response) {
          me.listPrincipal = response.data;
        })
        .catch(function(error) {
          console.log(error);
        })
        .finally(() => (this.loading = false));
    },

    /****************************************/
    /* Metodo que carga las variables cuando selcciono editar */
    /****************************************/
    m_editItem(item) {
      this.idD_TIPOS_CONTR = item.idD_TIPOS_CONTR;
      this.coD_TIPOS_CONTR = item.coD_TIPOS_CONTR;
      this.deS_TIPOS_CONTR = item.deS_TIPOS_CONTR;
      this.coD_USUAR_MODIF = item.coD_USUAR_MODIF;
      this.editedIndex = 1;
      this.dialog = true;
    },

    /****************************************/
    /* Metodo para limpiar campos */
    /****************************************/
    m_limpiar() {
      this.id = "";
      this.deS_TIPOS_CONTR = "";
      this.editedIndex = -1;
    },
    /****************************************/
    /* Metodo para guardar y actualizar     */
    /****************************************/
    m_guardar() {
      /** Si Validar devuevle 1 no realizara ninguna accion */
      if (this.m_Validar()) {
        return;
      }
      //Cdigo para Actualiar
      if (this.editedIndex > -1) {
        let me = this;
        let model = {
          idD_TIPOS_CONTR: me.idD_TIPOS_CONTR,
          coD_TIPOS_CONTR: me.coD_TIPOS_CONTR,
          deS_TIPOS_CONTR: me.deS_TIPOS_CONTR,
          idD_EMPRE: me.idd_EMPRE_Conexion,
          coD_USUAR_CREAC: me.cod_USUAR_Conexion,
          coD_USUAR_MODIF: me.cod_USUAR_Conexion,
        };
        axios
          .put("api/" + this.nameAxios + "/Actualizar", model)
          .then(function(response) {
            me.m_close();
            me.m_ListarPrincipal();
            me.m_limpiar();
          })
          .catch(function(error) {
            console.log(error);
          });
      } else {
        //Cdigo para guardar
        let me = this;
        let model = {
          coD_TIPOS_CONTR: me.coD_TIPOS_CONTR,
          deS_TIPOS_CONTR: me.deS_TIPOS_CONTR,
          idD_EMPRE: me.idd_EMPRE_Conexion,
          coD_USUAR_CREAC: me.cod_USUAR_Conexion,
          coD_USUAR_MODIF: me.cod_USUAR_Conexion,
        };
        axios
          .post("api/" + this.nameAxios + "/Guardar", model)
          .then(function(response) {
            me.m_close();
            me.m_ListarPrincipal();
            me.m_limpiar();
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },

    /****************************************/
    /* Metodo de validacin */
    /* OJO: El Dialogo de la vista debe estar dentro de un Form */
    /****************************************/
    m_Validar() {
      var validateForm = this.$refs.form.validate();
      return validateForm == true ? 0 : 1;
    },

    /****************************************/
    /* Metodo para abrir modal para activar o desactivar */
    /****************************************/
    m_activarDesactivarMostrar(item) {
      var accion = item.esT_REGIS == true ? 2 : 1;

      this.adModal = 1;
      this.adNombre = item.deS_TIPOS_CONTR;
      this.adId = item.idD_TIPOS_CONTR;

      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },

    /****************************************/
    /* Metodo para activar o desactivar     */
    /****************************************/
    m_activarDesactivar() {
      let me = this;
      axios
        .put("api/" + this.nameAxios + "/Estado/" + this.adId, {})
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.m_ListarPrincipal();
        })
        .catch(function(error) {
          console.log(error.request.response);
        });
    },

    /************************************************/
    /* Metodo para cerrar Modal de Activar/Desactivar */
    /************************************************/

    m_activarDesactivarCerrar() {
      this.adModal = 0;
    },
    /************************************************/
    /* Metodo para abrir pantalla de nuevo registro */
    /************************************************/

    m_AbrirNuevo() {
      this.$refs.form.reset();
      this.dialog = true;
    },
    /****************************************/
    /* Metodo para cerrar dialogo de Nuevo y Editar */
    /****************************************/
    m_close() {
      this.dialog = false;
      this.m_limpiar();
    },
  },
};
</script>
