
<template>
  <v-container style="padding-top:0px;margin-top:0px;" fluid>
    <v-row>
      <div class="col-lg-12 col-12">
        <v-card>
          <canvas id="grafico-lineal" style="width:500px !important;height:100px !important;" ></canvas>
          <v-card-actions>
            <v-card-text><span style="color:gray">Fichas charts</span></v-card-text>
          </v-card-actions>
        </v-card>
      </div>
    </v-row>
    <v-row>
      <div class="col-lg-4 col-12">
        <v-card>
          <canvas id="grafico-BarrasHorizontal"></canvas>
          <v-card-actions>
            <v-card-text><span style="color:gray">Accidentes charts</span></v-card-text>
          </v-card-actions>
        </v-card>
      </div>
      <div class="col-lg-4 col-12">
        <v-card>
            <canvas id="grafico-Circular"></canvas>
            <v-card-actions>
            <v-card-text><span style="color:gray">Trabajadores charts</span></v-card-text>
            </v-card-actions>
        </v-card>
      </div>
      <div class="col-lg-4 col-12">
        <v-card>
          <canvas id="grafico-BarraVertical"></canvas>
          <v-card-actions>
            <v-card-text><span style="color:gray">reas charts</span></v-card-text>
          </v-card-actions>
        </v-card>
      </div>
    </v-row>
  </v-container>
</template>

<script>
// @ is an alias to /src
import Chart from "chart.js";
import GraficoLineal from "@/components/Charts/GraficoLineal.js";
import GraficoCircular from "@/components/Charts/GraficoCircular.js";
import GraficoBarrasHorizontal from "@/components/Charts/GraficoBarrasHorizontal.js";
import GraficoBarrasVertical from "@/components/Charts/GraficoBarrasVertical.js";

export default {
  name: "Home",
  data() {
    return {
      /* Retorno componentes */
      GraficoLineal: GraficoLineal,
      GraficoCircular: GraficoCircular,
      GraficoBarrasHorizontal: GraficoBarrasHorizontal,
      GraficoBarrasVertical: GraficoBarrasVertical
    };
  },
  methods: {
    /* Metodos por cada chart */
    createChart(chartId, chartData) {
      const ctx = document.getElementById(chartId);
      const myChart = new Chart(ctx, {
        type: chartData.type,
        data: chartData.data,
        options: chartData.options
      });
    },
    createChart_Circular(chartId, chartData) {
      const ctx = document.getElementById(chartId);
      const myChart = new Chart(ctx, {
        type: chartData.type,
        data: chartData.data,
        options: chartData.options
      });
    },
    createChart_BarraHorizontal(chartIdH, chartDataH) {
      const ctx = document.getElementById(chartIdH);
      const myChart = new Chart(ctx, {
        type: chartDataH.type,
        data: chartDataH.data,
        options: chartDataH.options
      });
    },
    createChart_BarraVertical(chartIdB, chartDataB) {
      const ctx = document.getElementById(chartIdB);
      const myChart = new Chart(ctx, {
        type: chartDataB.type,
        data: chartDataB.data,
        options: chartDataB.options
      });
    },
  },
  mounted() {
    /* Charts Lineal */
    this.createChart("grafico-lineal", this.GraficoLineal);
     /* Charts Circular */
    this.createChart_Circular("grafico-Circular", this.GraficoCircular);
     /* Charts Barra Horizontal */
    this.createChart_BarraHorizontal("grafico-BarrasHorizontal", this.GraficoBarrasHorizontal);
    //  /* Charts Barra Vertical */
    this.createChart_BarraVertical("grafico-BarraVertical", this.GraficoBarrasVertical);
  }
};
</script>
