<template>
  <v-container>
    <!-- Barra Horizontal Titulo -->
    <v-app-bar app clipped-left color="#1e9ff2" v-if="logueado">
      <v-app-bar-nav-icon @click="drawer = !drawer" />
      <span class="title ml-3 mr-5" style="color:white">
        Myper &nbsp;
        <span class="font-weight-light subtitle-1">SSOMA</span>
      </span>
      <!--       <v-text-field solo-inverted flat hide-details label="Search" prepend-inner-icon="search" />
      -->
      <v-spacer />
      <v-spacer />
      <v-spacer />
      <v-spacer />
      <v-spacer />
      <v-spacer />
      <v-spacer />
      <v-spacer />
      <v-spacer />
      <v-spacer />
      <v-spacer />
      <v-spacer />
      <v-spacer />
      <v-spacer />
      <v-spacer />
      <v-spacer />
      <v-spacer />
      <span class="title ml-3 mr-5 subtitle-2" style="color:white">{{this.nomEmpresaConexion}}</span>
      <!--       <v-btn @click="salir" icon style="color:white">
        Cambiar Empresa<v-icon>logout</v-icon>
      </v-btn>-->
      <v-dialog v-model="dialog" max-width="500px">
        <template v-slot:activator="{ on }">
          <v-btn x-small color="black" dark class="mb-2 subtitle-1" v-on="on">
            <v-icon x-small>wrap_text</v-icon>
          </v-btn>
        </template>
        <v-card>
          <v-img
            class="white--text align-start"
            height="100px"
            src="https://cdn.vuetifyjs.com/images/cards/docks.jpg"
          >
            <v-card-title>
              <span class="headline">Cambiar Empresa</span>
            </v-card-title>
          </v-img>
          <v-card-text>
            <v-container grid-list-md>
              <v-row wrap>
                <v-col cols="12" sm="12" md="12">
                  <!-- Combo -->
                  <v-select
                    v-model="idD_EMPREList"
                    :items="listEmpresas"
                    label="Seleccione Empresa"
                  ></v-select>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <!-- Modal: Acciones -->
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
            <v-btn color="blue darken-1" text @click="guardar">Guardar</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-spacer />
      <v-btn @click="salir" v-if="logueado" icon style="color:white">
        Salir
        <v-icon>logout</v-icon>
      </v-btn>
      <!--       <v-btn :to="{name: 'login'}" v-else style="color:white" padding: 25px;>
        <v-icon>apps</v-icon>Login
      </v-btn>-->
      <v-spacer />
    </v-app-bar>

    <!-- Barra de navegacion -->
    <v-navigation-drawer v-model="drawer" app clipped color="grey lighten-4" v-if="logueado">
      <v-card class="mx-auto" width="300">
        <!--         <v-toolbar color="cyan" dark>
          <v-toolbar-title>
            <h6 class="font-weight-light">Hola {{this.user_Login}}</h6>
          </v-toolbar-title>
        </v-toolbar>-->

        <v-list>
          <a :href="'/'" style="text-decoration:none">
            <v-list-item>
              <v-list-item-icon>
                <v-icon medium>home</v-icon>
              </v-list-item-icon>

              <v-list-item-title class="subtitle-2">Home</v-list-item-title>
            </v-list-item>
          </a>

          <template  v-for="(item, i) in listSistemas">
            <v-list-group class="tile1" :key="i">
              <v-icon slot="prependIcon" small color="primary">{{item.noM_ICONO}}</v-icon>

              <template v-slot:activator id="idList">
                <v-list-item-title color="blue" class="subtitle-2">{{item.noM_SISTE}}</v-list-item-title>
              </template>
              <v-list-item
                class="tile"
                v-for="(itemOpcion, i) in listopciones.filter(word => word.idD_SISTE == item.idD_SISTE && word.idD_OPCIO_PADRE == 0)"
                :key="i"
                :to="{name: itemOpcion.noM_ACTIO}"
              >
                <template v-if="itemOpcion.esT_SUBME == true">
                  <v-list-group no-action sub-group>
                    <!--              <v-icon slot="prependIcon" small color="primary">{{item.noM_ICONO}}</v-icon> -->

                    <template v-slot:activator>
                      <v-list-item-content>
                        <v-list-item-title class="body-2 font-weight-light">
                          <span style="color:cornflowerblue"> {{itemOpcion.noM_OPCIO}}</span>
                         </v-list-item-title>
                      </v-list-item-content>
                    </template>

                    <v-list-item
                      v-for="(admin, i) in listopciones.filter(word => word.idD_OPCIO_PADRE == itemOpcion.idD_OPCIO)"
                      :key="i"
                      :to="{name: admin.noM_ACTIO}"
                      link
                      class="ps-12"
                    >
                      <v-list-item-title class="caption ps-0">
                        <span style="color:gray">{{admin.noM_OPCIO}}</span>
                      </v-list-item-title>
                      <!--          <v-list-item-icon>
                        <v-icon>people_outline</v-icon>
                      </v-list-item-icon>-->
                    </v-list-item>
                  </v-list-group>
                </template>
                <template v-else>
                     <v-list-item-content>
                  <v-list-item-title class="caption ps-12 font-weight-light">
                    <span style="color:gray">{{itemOpcion.noM_OPCIO}}</span>
                  </v-list-item-title>
                     </v-list-item-content>
                </template>
              </v-list-item>
            </v-list-group>
          </template>
        </v-list>
      </v-card>
    </v-navigation-drawer>
  </v-container>
</template>

<style >
/* .v-list-group {
  padding-left: 5px;
} */

#idList {
  color: red;
  padding-left: 5px;
}

.tile {
  margin: 5px;
  color: red;
  padding-left: 5px;
  border-radius: 4px;
}

/* .tile:hover {
  color: red;
}
.tile:active {
  background: rgb(115, 164, 187);
} */

/* v-list-group {
    padding-left: 0px;
} */
</style>

<script>
import axios from "axios";
import Estilos from "@/components/Home/Estilos.css";

export default {
  name: "MenuBar",
  data: () => {
    return {
      drawer: null,
      listSistemas: [],
      listopciones: [],
      listEmpresas: [],
      user_Login: "",
      styleObject: {
        fontSize: "caption"
      },
      codEmpresaConexion: "",
      nomEmpresaConexion: "",
      idD_EMPREList: "",
      dialog: false
    };
  },
  computed: {
    logueado() {
      return this.$store.state.usuario;
    },
    esAdministrador() {
      return (
        this.$store.state.usuario &&
        this.$store.state.usuario.rol == "Administrador"
      );
    },
    esAlmacenero() {
      return (
        this.$store.state.usuario &&
        this.$store.state.usuario.rol == "Almacenero"
      );
    },
    esVendedor() {
      return (
        this.$store.state.usuario && this.$store.state.usuario.rol == "Vendedor"
      );
    },
    listSistemasOrder: function() {
      return _.orderBy(this.listSistemas, "nuM_CORRE");
    }
  },

  created() {
    /****************************************/
    /* Operaciones que se cargan al iniciar la ventana */
    /****************************************/
    this.idD_EMPREList = localStorage.getItem("codEmpresa");
    this.codEmpresaConexion = localStorage.getItem("codEmpresa");
    this.nomEmpresaConexion = localStorage.getItem("nomEmpresa");
    this.user_Login = localStorage.getItem("nomUsuario");

    let token = localStorage.getItem("token");

    this.$store.dispatch("autoLogin");
    this.m_ListarEmpresasCombo();
    this.m_ListarOpciones();
    this.m_ListarSistemas();

    //this.user_Login = this.$store.state.usuario.nameid;
  },

  methods: {
    salir() {
      this.user_Login = "";
      this.$store.dispatch("salir");
    },
    m_ListarOpciones() {
      let me = this;
      var arrayListado = [];
      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      let header = { Authorization: "Bearer" + this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("api/Opciones/Listar", optionAxios)
        .then(response => {
          this.listopciones = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    m_ListarSistemas() {
      let me = this;
      var arrayListado = [];
      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      let header = { Authorization: "Bearer" + this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("api/Sistemas/ListarMenu", optionAxios)
        .then(response => {
          this.listSistemas = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    m_ListarEmpresasCombo() {
      let me = this;
      var arrayListado = [];
      var optionAxios = {
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + this.$store.state.token
        }
      };
      axios
        .get("api/Empresas/ListarComboInternas", optionAxios)
        .then(response => {
          arrayListado = response.data;
          arrayListado.map(function(x) {
            me.listEmpresas.push({
              text: x.noM_EMPRE_CORTA,
              value: x.idD_EMPRE
            });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    guardar() {
      var me = this;
      //si this.validar es igual a 1 significa que tiene un error y va retornar al modal
      localStorage.setItem("codEmpresa", me.idD_EMPREList);

      this.codEmpresaConexion = localStorage.getItem("codEmpresa");
      this.nomEmpresaConexion = localStorage.getItem("nomEmpresa");
      me.listEmpresas.forEach(function(word) {
        console.log(word.value);
        var prueba = me.codEmpresaConexion;
        if (me.codEmpresaConexion === word.value.toString()) {
          localStorage.setItem("nomEmpresa", word.text);
        }
      });
      this.codEmpresaConexion = localStorage.getItem("codEmpresa");
      this.nomEmpresaConexion = localStorage.getItem("nomEmpresa");
      location.reload();
      me.close();
    },

    close() {
      this.dialog = false;
    }
  }
};
</script>

