<template>
  <!-- align-start : alinea layout al centro-->
  <v-layout align-start>
    <v-flex>
      <!-- Codigo de vuetify-->
      <v-card class="mx-6">
        <!-- ":search": metodo buscar / items: el array donde se encuentran los datos en console () -->
        <v-data-table
          :headers="headers"
          :items="listPrincipal"
          :search="search"
          :loading="loading"
          sort-by="deS_ACTIV_ECONO"
          class="elevation-1"
        >
          <template v-slot:top>
            <v-toolbar flat color="white">
              <v-toolbar-title class="blue--text" v-text="nameOpcion"></v-toolbar-title>
              <v-divider class="mx-4" inset vertical></v-divider>
              <!-- v-spacer:spacio-->
              <v-spacer></v-spacer>
              <!-- Buscar / hide-details:ocultardetalles-->
              <v-text-field
                class="text-xs-center"
                v-model="search"
                append-icon="search"
                label="Bsqueda"
                single-line
                hide-details
              ></v-text-field>
              <v-spacer></v-spacer>
              <Buttons val_Param="Nuevo" @click.native="m_AbrirNuevo()" />
              <v-form ref="form" lazy-validation>
                <!-- Modal para Registrar / Actualizar -->
                <v-dialog v-model="dialog" max-width="500px">
                  <!-- Modal: Encabezado -->
                  <v-card>
                    <v-img
                      class="white--text align-start"
                      height="100px"
                      v-bind:src="imageDialogo"
                    >
                      <v-card-title>
                        <span class="headline">{{ formTitle }}</span>
                      </v-card-title>
                    </v-img>
                    <!-- Modal: contenido -->
                    <v-card-text>
                      <v-container grid-list-md>
                        <v-row wrap>
                          <v-col cols="12" sm="12" md="12">
                            <v-text-field :counter="400" 
                            :rules="[v => !!v || 'Actividad Econmica es requerida',
                              v => (v && v.length <= 400) || 'Nombre de Actividad Econmica debe ser menor a 400 caracteres',]"   
                            v-model="deS_ACTIV_ECONO" label="Descripcin"></v-text-field>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card-text>
                    <!-- Modal: Acciones -->
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <Buttons val_Param="Cancelar" @click.native="m_close" />
                      <Buttons val_Param="Guardar" @click.native="m_guardar" />
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-form>
              <!-- Modal para Activar / Desactivar -->
              <v-dialog v-model="adModal" max-width="400">
                <v-card>
                  <v-card-title class="headline" v-if="adAccion==1">Activar Item?</v-card-title>
                  <v-card-title class="headline" v-if="adAccion==2">Desactivar Item?</v-card-title>
                  <v-card-text>
                    Ests a punto de
                    <span v-if="adAccion==1">Activar</span>
                    <span v-if="adAccion==2">Desactivar</span>
                    el tem {{adNombre}}
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <Buttons val_Param="Cancelar" @click.native="m_activarDesactivarCerrar()"></Buttons>
                    <Buttons
                      v-if="adAccion == 1"
                      val_Param="Activar"
                      @click.native="m_activarDesactivar()"
                    ></Buttons>
                    <Buttons
                      v-if="adAccion == 2"
                      val_Param="Desactivar"
                      @click.native="m_activarDesactivar()"
                    ></Buttons>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>
          <template v-slot:item.esT_REGIS="{ item }">
            <!-- Paso el parametro del registro -->
            <Icons
              v-bind:val_Param="item.esT_REGIS"
              small
              @click.native="m_activarDesactivarMostrar(item)"
            />
          </template>

          <template v-slot:item.opciones="{ item }">
            <v-icon small class="mr-2" @click="m_editItem(item)">edit</v-icon>
          </template>
          <template v-slot:no-data>
            <Buttons val_Param="Refrescar" @click.native="m_ListarPrincipal()"></Buttons>
          </template>
        </v-data-table>
      </v-card>
      <!-- -->
    </v-flex>
  </v-layout>
</template>

<script src = "azure-storage.blob.js">
</script>
<script>
import axios from "axios";
import Buttons from "@/components/Buttons/Buttons.vue";
import Icons from "@/components/Buttons/Icons.vue";

export default {
  //name: "Actividad Econmica",
  components: {
    Buttons,
    Icons
  },
  data: () => {
    return {
      /** Variables de conexion NO TOCAR*/
      imageDialogo: "",
      cod_USUAR_Conexion: "",
      idd_EMPRE_Conexion: 0,
      listPrincipal: [],

      /* Variables de la pantalla  NO TOCAR*/
      search: "",
      editedIndex: -1,
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: "",
      dialog: false,
      drawer: null,
      loading: true,

      /* Nombres de la opcin y el controller BackEnd */
      nameOpcion: "Actividad Econmica", //Nombre que se muestra en la pantalla
      nameAxios: "ActivEconomicas", //Nombre que se utiliza para buscar el backend

      /** Columnas de la tabla */
      headers: [
        {
          text: "Condicin ",
          value: "esT_REGIS",
          sortable: false,
          align: "center"
        },
        { text: "Descripcin", value: "deS_ACTIV_ECONO", sortable: true },
        {
          text: "Opciones",
          value: "opciones",
          sortable: false,
          align: "center"
        }
      ],

      /*Variables para la entidad a tratar*/
      idD_ACTIV_ECONO: "",
      coD_ACTIV_ECONO: "",
      deS_ACTIV_ECONO: "",
      coD_USUAR_CREAC: "",
      feC_USUAR_CREAC: "",
      coD_USUAR_MODIF: "",
      feC_USUAR_MODIF: "",
      esT_REGIS: ""
    };
  },

  computed: {
    /****************************************/
    /* Metodo que funcionan dentro de la ventana */
    /****************************************/
    formTitle() {
      return this.editedIndex === -1
        ? "Nueva " + this.nameOpcion
        : "Actualizar " + this.nameOpcion;
    }
  },

  watch: {
    dialog(val) {
      val || this.m_close();
    }
  },

  created() {
    /****************************************/
    /* Operaciones que se cargan al iniciar la ventana */
    /****************************************/
    this.idd_EMPRE_Conexion = parseInt(localStorage.getItem("codEmpresa"));
    this.cod_USUAR_Conexion = localStorage.getItem("codUsuario");
    this.imageDialogo = this.$imageDialog;
    this.m_ListarPrincipal();
  },

  methods: {
    /****************************************/
    /* Metodo para listar Empresas       */
    /****************************************/
    m_ListarPrincipal() {
      let me = this;
      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      let header = { Authorization: "Bearer" + this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("api/" + this.nameAxios + "/Listar", optionAxios)
        .then(function(response) {
          me.listPrincipal = response.data;
        })
        .catch(function(error) {
          console.log(error);
        })
        .finally(() => (this.loading = false));
    },
    /****************************************/
    /* Metodo que carga las variables cuando selcciono editar */
    /****************************************/
    m_editItem(item) {
      this.idD_ACTIV_ECONO = item.idD_ACTIV_ECONO;
      this.coD_ACTIV_ECONO = item.coD_ACTIV_ECONO;
      this.deS_ACTIV_ECONO = item.deS_ACTIV_ECONO;
      this.coD_USUAR_CREAC = item.coD_USUAR_CREAC;
      this.coD_USUAR_MODIF = item.coD_USUAR_MODIF;
      this.editedIndex = 1;
      this.dialog = true;
    },

    /****************************************/
    /* Metodo para limpiar campos          */
    /****************************************/
    m_limpiar() {
      this.id = "";
      this.coD_ACTIV_ECONO = "";
      this.deS_ACTIV_ECONO = "";
      this.editedIndex = -1;
    },

    /****************************************/
    /* Metodo para guardar/actualizar       */
    /****************************************/
    m_guardar() {
      /** Si Validar devuevle 1 no realizara ninguna accion */
      if (this.m_Validar()) {
        return;
      }
      if (this.editedIndex > -1) {
        let me = this;
        axios
          .put("api/" + this.nameAxios + "/Actualizar", {
            idD_ACTIV_ECONO: me.idD_ACTIV_ECONO,
            coD_ACTIV_ECONO: me.coD_ACTIV_ECONO,
            deS_ACTIV_ECONO: me.deS_ACTIV_ECONO,
            coD_USUAR_CREAC: this.cod_USUAR_Conexion,
            coD_USUAR_MODIF: this.cod_USUAR_Conexion
          })
          .then(function(response) {
            me.m_close();
            me.m_ListarPrincipal();
            me.m_limpiar();
          })
          .catch(function(error) {
            console.log(error);
            console.log(error.request);
            console.log(error.request.response);
          });
      } else {
        //Cdigo para guardar
        var me = this;
        axios
          .post("api/" + this.nameAxios + "/Guardar", {
            coD_ACTIV_ECONO: me.coD_ACTIV_ECONO,
            deS_ACTIV_ECONO: me.deS_ACTIV_ECONO,
            coD_USUAR_CREAC: this.cod_USUAR_Conexion,
            coD_USUAR_MODIF: this.cod_USUAR_Conexion
          })
          .then(function(response) {
            me.m_close();
            me.m_ListarPrincipal();
            me.m_limpiar();
          })
          .catch(function(error) {
            console.log(error);
            console.log(error.request.response);
          });
      }
    },

    /****************************************/
    /* Metodo de validacin */
    /* OJO: El Dialogo de la vista debe estar dentro de un Form */
    /****************************************/
    m_Validar() {
      var validateForm = this.$refs.form.validate();
      return validateForm == true ? 0 : 1;
    },

    /****************************************/
    /* Metodo para abrir modal para activar o desactivar */
    /****************************************/
    m_activarDesactivarMostrar(item) {
      var accion = item.esT_REGIS == true ? 2 : 1;

      this.adModal = 1;
      this.adNombre = item.deS_ACTIV_ECONO;
      this.adId = item.idD_ACTIV_ECONO;

      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },

    /****************************************/
    /* Metodo para activar o desactivar     */
    /****************************************/
    m_activarDesactivar() {
      let me = this;
      axios
        .put("api/" + this.nameAxios + "/Estado/" + this.adId, {})
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.m_ListarPrincipal();
        })
        .catch(function(error) {
          console.log(error.request.response);
        });
    },

    /************************************************/
    /* Metodo para cerrar Modal de Activar/Desactivar */
    /************************************************/

    m_activarDesactivarCerrar() {
      this.adModal = 0;
    },
    /************************************************/
    /* Metodo para abrir pantalla de nuevo registro */
    /************************************************/

    m_AbrirNuevo() {
      this.$refs.form.reset();
      this.dialog = true;
    },
    /****************************************/
    /* Metodo para cerrar dialogo de Nuevo y Editar */
    /****************************************/
    m_close() {
      this.dialog = false;
      this.m_limpiar();
    },
  }
};
</script>