<template>
  <v-data-table
    :headers="headers"
    :items="listPrincipal"
    item-key="idD_PERFI_OPCIO"
    group-by="noM_PERFI"
    class="elevation-1"
    :loading="loading"
  >
    <template v-slot:group.header="{ items, group, toggle, isOpen }">
      <td>
        <v-btn @click="toggle" small icon :ref="group" :data-open="isOpen">
          <v-icon v-if="isOpen">mdi-chevron-up</v-icon>
          <v-icon v-else>mdi-chevron-down</v-icon>
        </v-btn>
        {{ items[0].noM_PERFI }}
      </td>
      <td class="text-xs-right"></td>
      <td class="text-xs-right"></td>
      <td class="text-xs-right"></td>
      <td class="text-xs-right">
        <v-btn gray @click="m_AbrirAgregarNuevo(items[0].idD_PERFI)">Agregar Opcin</v-btn>
      </td>
      <!--       </template> -->

      <!--    <template v-slot:group.header="{ group, headers, toggle, isOpen }">
      <td :colspan="headers.length">
        <v-btn @click="toggle" small icon :ref="group" :data-open="isOpen">
          <v-icon v-if="isOpen">mdi-chevron-up</v-icon>
          <v-icon v-else>mdi-chevron-down</v-icon>
        </v-btn>
        {{ group }}
       
      </td>
      <v-btn gray @click="m_AbrirAgregarNuevo(group)" >Agregar Opcin</v-btn>
      -->
      <v-form ref="form" lazy-validation>
        <v-dialog v-model="dialogNew" :retain-focus="false" max-width="500px">
          <v-card>
            <v-img
              class="white--text align-start"
              height="100px"
              src="https://cdn.vuetifyjs.com/images/cards/docks.jpg"
            >
              <v-card-title>
                <span class="headline">{{ formTitle }}</span>
              </v-card-title>
            </v-img>
            <v-card-text>
              <v-container grid-list-md>
                <v-data-table
                  v-model="selected"
                  :headers="headersOpcion"
                  :items="listAgregarOpcion"
                  :single-select="false"
                  item-key="noM_OPCIO"
                  show-select
                  class="elevation-1"
                  hide-default-footer
      
                ></v-data-table>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <Buttons val_Param="Cancelar" @click.native="m_close" />
              <Buttons val_Param="Guardar" @click.native="m_GuardarOpcionesNuevas" />
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-form>
    </template>

    <!-- Listado de PErfiles -->
    <template v-slot:top>
      <v-toolbar flat color="white">
        <v-toolbar-title class="blue--text">Perfiles</v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <template>
          <v-btn small color="warning" dark class="mb-2" @click="m_Regresar()">Regresar</v-btn>
        </template>
        <!-- v-spacer:spacio-->
        <v-spacer></v-spacer>

        <!-- Buscar / hide-details:ocultardetalles-->
        <v-text-field
          class="text-xs-center"
          v-model="search"
          append-icon="search"
          label="Bsqueda"
          single-line
          hide-details
        ></v-text-field>
        <v-spacer></v-spacer>

        <!-- Modal para Activar / Desactivar -->
        <v-dialog v-model="adModal" max-width="290">
          <v-card>
            <v-card-title class="headline" v-if="adAccion==1">Activar Item?</v-card-title>
            <v-card-title class="headline" v-if="adAccion==2">Desactivar Item?</v-card-title>
            <v-card-text>
              Ests a punto de
              <span v-if="adAccion==1">Activar</span>
              <span v-if="adAccion==2">Desactivar</span>
              el tem {{adNombre}}
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                style="color:white"
                color="green darken-1"
                @click="activarDesactivarCerrar"
              >Cancelar</v-btn>
              <v-btn
                v-if="adAccion==1"
                style="color:white"
                color="orange darken-4"
                @click="m_activarDesactivar"
              >Activar</v-btn>
              <v-btn
                v-if="adAccion==2"
                style="color:white"
                color="orange darken-4"
                @click="m_activarDesactivar"
              >Desactivar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

        <!-- Modal para Registrar / Actualizar -->
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on }">
            <v-btn color="success" dark class="mb-2" v-on="on">Nuevo Registro</v-btn>
          </template>
          <!-- Modal: Encabezado -->
          <v-card>
            <v-img
              class="white--text align-start"
              height="100px"
              src="https://cdn.vuetifyjs.com/images/cards/docks.jpg"
            >
              <v-card-title>
                <span class="headline">{{ formTitle }}</span>
              </v-card-title>
            </v-img>
            <!-- Modal: contenido -->
            <v-card-text>
              <v-container grid-list-md>
                <v-row wrap>
                  <v-col cols="12" sm="12" md="12">
                    <v-text-field v-model="noM_PERFI" label="Descripcin"></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <!-- Modal: Acciones -->
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="btnCancelar" text @click="close">Cancelar</v-btn>
              <v-btn color="btnGuardar" text @click="m_Guardar">Guardar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>

    <template v-slot:item.esT_GRABA="{ item }">
      <template v-if="item.esT_GRABA">
        <v-btn text class="gray" @click="m_ActivarDesactivar(item, 'EST_GRABA')">
          <v-icon color="blue">check_box</v-icon>
        </v-btn>
      </template>
      <template v-else>
        <v-btn text class="gray" @click="m_ActivarDesactivar(item, 'EST_GRABA')">
          <v-icon>check_box_outline_blank</v-icon>
        </v-btn>
      </template>
    </template>

    <template v-slot:item.esT_ACTUA="{ item }">
      <template v-if="item.esT_ACTUA">
        <v-btn text class="gray" @click="m_ActivarDesactivar(item, 'EST_ACTUA')">
          <v-icon color="blue">check_box</v-icon>
        </v-btn>
      </template>
      <template v-else>
        <v-btn text class="gray" @click="m_ActivarDesactivar(item, 'EST_ACTUA')">
          <v-icon>check_box_outline_blank</v-icon>
        </v-btn>
      </template>
    </template>

    <template v-slot:item.esT_ELIMI="{ item }">
      <template v-if="item.esT_ELIMI">
        <v-btn text class="gray" @click="m_ActivarDesactivar(item, 'EST_ELIMI')">
          <v-icon color="blue">check_box</v-icon>
        </v-btn>
      </template>
      <template v-else>
        <v-btn text class="gray" @click="m_ActivarDesactivar(item, 'EST_ELIMI')">
          <v-icon>check_box_outline_blank</v-icon>
        </v-btn>
      </template>
    </template>

    <template v-slot:item.detalle="{ item }">
      <template>
        <v-btn text class="gray" @click="m_EliminarOpcion(item.idD_PERFI_OPCIO)">
          <v-icon left color="red">mdi-close-circle-outline</v-icon>Eliminar
        </v-btn>
      </template>
    </template>
    <template v-slot:no-data>
      <v-btn color="primary" @click="m_ListarPrincipal">Reset</v-btn>
    </template>
    <template v-slot:no-data>NO DATA HERE!</template>

    <template v-slot:no-results>NO RESULTS HERE!</template>
  </v-data-table>
</template>


<script>
import axios from "axios";
import Buttons from "@/components/Buttons/Buttons.vue";

export default {
  props: {
    // Simple type validation
    item_Param: Number
  },
  components: {
    Buttons
  },
  //name: "Actividad Econmica",
  data: () => {
    return {
      /** Variables de conexion NO TOCAR*/
      imageDialogo: "",
      cod_USUAR_Conexion: "",
      idd_EMPRE_Conexion: 0,
      listPrincipal: [],

      /* Variables de la pantalla  NO TOCAR*/
      search: "",
      editedIndex: -1,
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: "",
      drawer: null,
      dialog: false,
      loading: true,

      /** Variables del listado general */
      nameOpcion: "Perfil", //Nombre que se muestra en la pantalla
      nameAxios: "Perfiles", //Nombre que se utiliza para buscar el backend

      /** */
      listPrincipal: [],
      dialogNew: false,
      headers: [
        { text: "Opcin", value: "noM_OPCIO", sortable: true },
        { text: "Grabar", value: "esT_GRABA", sortable: true },
        { text: "Actualizar", value: "esT_ACTUA", sortable: true },
        { text: "Eliminar", value: "esT_ELIMI", sortable: true },
        { text: "", value: "detalle", sortable: false }
      ],
      /* Variables de la lista */
      userName: "",
      idD_EMPRE: 0,
      noM_PERFI: "",
      noM_OPCIO: "",
      idD_SISTE: 0,
      idD_PERFI_OPCIO: 0,
      idD_OPCIO: 0,
      esT_GRABA: false,
      esT_ACTUA: false,
      esT_ELIMI: false,

      /** variables Dialogo Nuevo*/
      singleSelect: false,
      selected: [],
      listAgregarOpcion: [],
      listAddOpcion: [],
      headersOpcion: [
        { text: "Opcin", value: "noM_OPCIO", sortable: true },
        { text: "", value: "detalle", sortable: false }
      ],
      idD_PERFI_SELEC: 0,
    };
  },

  computed: {
    /****************************************/
    /* Metodo que funcionan dentro de la ventana */
    /****************************************/
    formTitle() {
      return this.editedIndex === -1
        ? "Nuevo " + this.nameOpcion
        : "Actualizar " + this.nameOpcion;
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    /****************************************/
    /* Operaciones que se cargan al iniciar la ventana */
    /****************************************/
    this.idD_SISTE = this.item_Param;

    this.idd_EMPRE_Conexion = parseInt(localStorage.getItem("codEmpresa"));
    this.cod_USUAR_Conexion = localStorage.getItem("codUsuario");
    this.m_ListarPrincipal();
  },

  methods: {
    m_GuardarOpcionesNuevas() {

      //var pruebas = item;
      //var prueba = this.listAgregarOpcion;
      //var prueba1 = this.selected;
      var me = this;
      this.selected.forEach(function(word) {
        var model = {
          idD_OPCIO: word.idD_OPCIO,
          idD_PERFI: me.idD_PERFI_SELEC,
          coD_USUAR_CREAC: me.cod_USUAR_Conexion,
          coD_USUAR_MODIF: me.cod_USUAR_Conexion
        };
        me.listAddOpcion.push(model);

        /*    var prueba = me.codEmpresaConexion;
        if (me.codEmpresaConexion === word.value.toString()) {
          localStorage.setItem("nomEmpresa", word.text);
        } */
      });
      var optionAxios = {
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + this.$store.state.token
        }
      };
      axios
        .post(
          "api/OpcionesPorPerfiles/AgregarOpciones",
          me.listAddOpcion,
          optionAxios
        )
        .then(function(response) {
          me.dialogNew = false;
          me.m_ListarPrincipal();
          me.m_limpiar();
          

        })
        .catch(function(error) {
          console.log(error);
        });
        this.selected = [];
    },
    m_AbrirAgregarNuevo(item) {
      this.selected = [];
      this.listAddOpcion = [];
      this.idD_PERFI_SELEC = item;
      this.dialogNew = true;
      this.m_ListarOpcionesNuevas(item);
    },

    m_ListarOpcionesNuevas(id) {
      let me = this;
      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };

      axios
        .get("/api/Opciones/AgregarOpciones/" + id, optionAxios)
        .then(function(response) {
          console.log(response);
          me.listAgregarOpcion = response.data;
        })
        .catch(function(error) {
          console.log(error);
        })
        .finally(() => (this.loading = false));
    },

    m_EliminarOpcion(id) {
      let me = this;
      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };
      axios
        .delete("/api/Perfiles/EliminarOpcion/" + id, optionAxios)
        .then(function(response) {
          me.m_ListarPrincipal();
          me.close();
        })
        .catch(function(error) {
          console.log(error);
          console.log(error.request);
        });
    },

    m_ListarPrincipal() {
      let me = this;
      var optionAxios = {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        }
      };

      axios
        .get(
          "api/OpcionesPorPerfiles/ListarPorSistema?idD_SISTE=" +
            this.idD_SISTE +
            "&idD_EMPRE=" +
            this.idd_EMPRE_Conexion,
          optionAxios
        )
        .then(function(response) {
          //console.log(response);
          me.listPrincipal = response.data;
        })
        .catch(function(error) {
          console.log(error);
        })
        .finally(() => (this.loading = false));
    },

    m_ActivarDesactivar(item, EST_VARIA) {
      var prueba = item;
      let me = this;
      let model = {
        idD_PERFI_OPCIO: item.idD_PERFI_OPCIO,
        esT_GRABA: item.esT_GRABA == true ? false : true,
        esT_ACTUA: item.esT_ACTUA == true ? false : true,
        esT_ELIMI: item.esT_ELIMI == true ? false : true,
        coD_USUAR_CREAC: me.cod_USUAR_Conexion,
        coD_USUAR_MODIF: me.cod_USUAR_Conexion
      };
      var optionAxios = {
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + this.$store.state.token
        }
      };
      axios
        .put(
          "api/OpcionesPorPerfiles/Actualizar" + EST_VARIA,
          model,
          optionAxios
        )
        .then(function(response) {
          me.m_ListarPrincipal();
          me.m_limpiar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    m_Guardar() {
      //si this.validar es igual a 1 significa que tiene un error y va retornar al modal

      if (this.editedIndex > -1) {
        let me = this;
        axios
          .put("api/ActivEconomicas/Actualizar", {
            idD_OPCIO: me.idD_OPCIO,
            idD_SISTE: me.idD_SISTE_OPCIO,
            noM_OPCIO: me.noM_OPCIO,
            noM_ACTIO: me.noM_ACTIO,
            nuM_CORRE: parseInt(me.nuM_CORRE),
            esT_SUBME: me.esT_SUBME,
            idD_OPCIO_PADRE: me.idD_OPCIOList
          })
          .then(function(response) {
            me.m_ListarPrincipal();
            me.close();
          })
          .catch(function(error) {
            console.log(error);
            console.log(error.request);
            console.log(error.request.response);
          });
      } else {
        var me = this;
        let model = {
          coD_PERFI: me.coD_PERFI,
          noM_PERFI: me.noM_PERFI,
          idD_EMPRE: me.idd_EMPRE_Conexion,
          idD_SISTE: me.idD_SISTE,
          coD_USUAR_CREAC: me.cod_USUAR_Conexion,
          coD_USUAR_MODIF: me.cod_USUAR_Conexion
        };
        axios
          .post("api/Perfiles/Guardar", model)
          .then(function(response) {
            me.close();
            me.m_ListarPrincipal();
            me.m_limpiar();
          })
          .catch(function(error) {
            console.log(error);
            console.log(error.request.response);
          });
      }
    },

    mostrarNuevo(id) {
      this.idD_SISTE_OPCIO = id;
      this.listarOpcionesPorSistema(id);
      this.listarOpcionesPadresPorSistema(id);
      this.vContainerSistema = 1;
    },

    m_Regresar() {
      this.$emit("CerrarDialogo", "prueba");
      this.vContainerSistema = 0;
    },

    /****************************************/
    /* Metodo para cerrar dialogo          */
    /****************************************/
    close() {
      this.dialog = false;
      this.m_limpiar();
    },

    /****************************************/
    /* Metodo para limpiar campos          */
    /****************************************/
    m_limpiar() {
      this.id = "";
      this.validaMensaje = [];
      this.editedIndex = -1;
    },

    /****************************************/
    /* Metodo para abrir modal para activar o desactivar */
    /****************************************/

    activarDesactivarSisMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.noM_SISTE;
      this.adId = item.idD_SISTE;

      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },

    /****************************************/
    /* Metodo para abrir modal para activar o desactivar */
    /****************************************/

    activarDesactivarMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.noM_OPCIO;
      this.adId = item.idD_OPCIO;

      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },

    /****************************************/
    /* Metodo para cerrar modal si no se desea activar/desactivar */
    /****************************************/
    activarDesactivarCerrar() {
      this.adModal = 0;
    },
    /****************************************/
    /* Metodo para cerrar dialogo de Nuevo y Editar */
    /****************************************/
    m_close() {
      this.dialogNew = false;
      this.m_limpiar();
    }
  }
};
</script>